import { Injectable, OnInit } from "@angular/core";
import { MenuKey } from '../constants/menu.const';
import { MenuTab, ExtendedMainMenuGroup, MenuItem } from '../models/base.model';

@Injectable({ providedIn: 'root' })
export class MenuService {
    public tabs: { name: string, menu: string, subName: string, items: ExtendedMainMenuGroup[] }[] = [];
    public currentSupporterId: string;
    public currentSelectedSupplierName: string;
    public currentSelectedSupplierId: string;

    public init(menuTabs: MenuTab[], callback: () => any, set: (role: string) => MenuItem): void {
        let menuTabbedItems = menuTabs;
        this.tabs = menuTabbedItems.reduce((result, current) => {
            let item = set
                ? set(current.role)
                : new MenuItem({
                    menu: current.role,
                    items: current.items,
                    subName: current.role,
                    name: current.role
                });
            if (item.name) {
                result.push(item);
            }
            return result;
        }, []);
        if (!this.tabs || !this.tabs[0]) {
            this.tabs = [];
        }
        if (callback) callback();
    }

    public loadMenuItems(menu?: string) {
        if (!menu) {
            return this.tabs.reduce((result, current) => {
                result.push(...current.items);
                return result;
            }, []);
        }
        const tab = this.tabs.find(x => x.menu === menu);
        if (!tab) {
            return;
        }
        return tab.items;
    }

    public loadFirstTabItems(key: string) {
        let currentMenu = this.tabs.find(s => s.menu.toLowerCase() == key.toLowerCase());
        if (!currentMenu) {
            return [];
        }
        return currentMenu.items;
    }

    public loadFirstTab() {
        return this.tabs[0];
    }

    public getTabs() {
        return this.tabs;
    }
}