import { AfterContentInit, ContentChildren, QueryList, Input, Output, EventEmitter, OnDestroy, Directive } from '@angular/core';
import { RadioItemComponent } from './radio-item/radio-item.component';

@Directive({
    selector: 'katana-radio-items'
})

export class RadioComponent implements AfterContentInit, OnDestroy {
    @ContentChildren(RadioItemComponent) public items: QueryList<RadioItemComponent>;
    @Output() public modelChange: EventEmitter<any> = new EventEmitter();
    @Input() public model: any;
    @Input() public label: string;
    @Input() public disabled: boolean;
    @Input() public formControlName: string;
    @Input() public validationName: string;

    ngAfterContentInit(): void {
        this.items.forEach((s, index) => {
            s.groupIndex = index;
            if (this.model !== null && this.model !== undefined && this.model === s.value) {
                s.checked = true;
            }
            s.registerSelectedCallback((componentRef: RadioItemComponent) => {
                this.items.forEach(item => {
                    item.checked = false;
                });
                let selectedItem = this.items.find(y => y.groupIndex === componentRef.groupIndex);
                selectedItem.checked = true;
                this.model = componentRef.value;
                this.modelChange.emit(componentRef.value);
            });
        });
    }

    ngOnDestroy(): void {
        this.modelChange.emit(null);
    }
}
