import { Component, ViewChild, TemplateRef, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'katana-dropdown-button',
  templateUrl: './dropdown-button.component.html',
  styleUrls: ['./dropdown-button.component.scss']
})

export class DropdownButtonComponent {
  @ViewChild(TemplateRef, { static: true }) public template: TemplateRef<any>;
  @Input() public title: string;
  @Input() public disabled: boolean;
  @Input() public lazyload: boolean = false;
  @Input() public customClass: string;
  @Input() public icon: string;
  @Input() public loadingIcon: string = 'fa fa-spinner icon-loader';
  @Output() public execute: EventEmitter<Function> = new EventEmitter();
  public loading: boolean;

  public handleAction($event) {
    if (this.loading) {
      $event.preventdefault();
      return;
    };

    if (this.lazyload) {
      this.loading = true;
      this.execute.emit(() => { this.loading = false; });
    } else {
      this.execute.emit();
    }
  }
}
