import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MediaViewerComponent } from './media-viewer/media-viewer.component';
import { FullMediaComponent } from './full-media/full-media.component';
import { FormatterModule } from '../formatter/formatter.module';

@NgModule({
  declarations: [MediaViewerComponent, FullMediaComponent],
  imports: [
    CommonModule,
    FormatterModule
  ],
  exports: [MediaViewerComponent, FullMediaComponent],
  entryComponents: [FullMediaComponent]
})

export class MediaViewerModule { }
