import { Injectable } from '@angular/core';
import { BsModalService, BsModalRef } from 'ngx-bootstrap';
import { NotificationViewModel, TemplateViewModel, ConfirmViewModel } from './modal.model';
import { NotificationComponent } from './components/notification/notification.component';
import { ConfirmComponent } from './components/confirm/confirm.component';
import { TemplateComponent } from './components/template/template.component';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';

@Injectable({ providedIn: 'root' })
export class ModalService {
  private _modalInstance: BsModalRef;
  constructor(
    private _bsModalService: BsModalService,
    private Ng4LoadingSpinnerService: Ng4LoadingSpinnerService
  ) { }

  public showLoading(): void {
    this.Ng4LoadingSpinnerService.show();
  }

  public hideLoading(): void {
    this.Ng4LoadingSpinnerService.hide();
  }

  public showNotificationDialog(data: NotificationViewModel): void {
    var customCss = data.customSize + (data.center ? ' modal-dialog-centered modal-xlg' : '');
    this._modalInstance = this._bsModalService.show(NotificationComponent, {
      backdrop: 'static',
      class: customCss,
      keyboard: false,
      ignoreBackdropClick: data.ignoreBackdropClick,
      animated: true
    });
    this._modalInstance.content.data = data;
  }

  public showConfirmDialog(data: ConfirmViewModel): void {
    var customCss = data.customSize + (data.center ? ' modal-dialog-centered modal-xlg' : '');
    this._modalInstance = this._bsModalService.show(ConfirmComponent, {
      backdrop: 'static',
      class: customCss,
      keyboard: false,
      ignoreBackdropClick: data.ignoreBackdropClick,
      animated: true
    });
    this._modalInstance.content.data = data;
  }

  public showTemplateDialog(data: TemplateViewModel): void {
    var customCss = data.customSize + (data.center ? ' modal-dialog-centered modal-xlg' : '');
    this._modalInstance = this._bsModalService.show(TemplateComponent, {
      backdrop: 'static',
      class: customCss,
      keyboard: false,
      ignoreBackdropClick: data.ignoreBackdropClick,
      animated: true
    });
    this._modalInstance.content.data = data;
  }
}
