import { BaseTemplate } from '../models';

export class LoadingViewModel {
  threshold: number = 250;
  timeout: number = 999999;
  template?: string;
  loadingText?: string;
  constructor(init?: Partial<LoadingViewModel>) {
    Object.assign(this, init);
  }
}

export class NotificationViewModel {
  center?: boolean;
  title?: string;
  customSize?: string;
  ignoreBackdropClick?: boolean;
  message?: string;
  btnTitle?: string;
  callback?: (close: () => any) => any;
  autoClose?: boolean;
  constructor(init?: Partial<NotificationViewModel>) {
    Object.assign(this, init);
  }
}

export class ConfirmViewModel {
  center?: boolean;
  title?: string;
  icon?: string;
  customSize?: string;
  ignoreBackdropClick?: boolean;
  message?: string;
  isDeleted?: boolean;
  btnAcceptTitle?: string;
  btnCancelTitle?: string;
  autoClose?: boolean;
  acceptCallback?: (close: () => any) => any;
  cancelCallback?: (close: () => any) => any;
  constructor(init?: Partial<ConfirmViewModel>) {
    Object.assign(this, init);
  }
}

export class TemplateViewModel extends BaseTemplate {
  center?: boolean;
  title?: string;
  mode?: string;
  customSize?: string;
  ignoreBackdropClick?: boolean;
  request?: any;
  icon?: string;
  btnAcceptTitle?: string;
  btnCancelTitle?: string;
  autoClose?: boolean;
  acceptCallback?: (response?: any, close?: () => any, provider?: any, ) => any;
  cancelCallback?: (response?: any, close?: () => any, provider?: any) => any;
  constructor(init?: Partial<TemplateViewModel>) {
    super();
    Object.assign(this, init);
  }
}