import { Component, Input, Output, EventEmitter, SimpleChanges, OnDestroy } from '@angular/core';

@Component({
    selector: 'katana-checkbox',
    templateUrl: './checkbox.component.html',
    styleUrls: ['./checkbox.component.scss']
})

export class CheckboxComponent implements OnDestroy {
    @Input() public model: boolean;
    @Input() public title: string;
    @Input() public customModel: any;
    @Output() public customModelChange: EventEmitter<any> = new EventEmitter();
    @Input() public customModelTransformation: (value: any) => any;
    @Input() public disabled: boolean;
    @Input() public direction: 'vertical' | 'horizontal' = 'horizontal';
    @Input() validationName: string;
    @Input() validationScope: string;
    @Input() emitNullOnDestroy: boolean = false;
    @Output() public modelChange: EventEmitter<boolean> = new EventEmitter();

    ngOnChanges(changes: SimpleChanges) {
        if (changes.customModel) {
            const value = changes.customModel.currentValue;
            this.model = this.customModelTransformation(value);
            this.customModelChange.emit(value);
        }
    }

    public onModelChange(value: boolean) {
        if (this.customModelTransformation) {
            const newValue = this.customModelTransformation(value);
            this.customModelChange.emit(newValue);
        } else {
            this.modelChange.emit(value);
        }
    }

    ngOnDestroy() {
        if (this.emitNullOnDestroy === true)
            this.modelChange.emit(null);
    }
}
