import { Component, Input, ViewChild, TemplateRef, OnInit } from '@angular/core';
import { Observable, of } from 'rxjs';
import { TableOption, TableColumnType } from '../../table/table.model';
import { FileResponse, FileRequest, FileViewModel } from '../../file/file.model';
import { TableComponent } from '../../table/table.component';

@Component({
  selector: 'katana-gallery',
  templateUrl: './gallery.component.html',
  styleUrls: ['./gallery.component.scss']
})

export class GalleryComponent implements OnInit {
  @Input() public items: (request: FileRequest) => Observable<FileResponse>;
  @Input() public headers: string[];
  @ViewChild('imageTemplate', { static: true }) public imageTemplate: TemplateRef<any>;
  @ViewChild('sizeTemplate', { static: true }) public sizeTemplate: TemplateRef<any>;
  @ViewChild('nameTemplate', { static: true }) public nameTemplate: TemplateRef<any>;
  @ViewChild('tableRef', { static: true }) public tableRef: TableComponent;
  public option: TableOption;

  ngOnInit(): void {
    this.initTable();
  }

  public isValid(): boolean {
    return this.tableRef.selectedItems.length > 0;
  }

  public callback(): Observable<FileViewModel[]> {
    return of(this.tableRef.selectedItems);
  }

  private initTable(): void {
    this.option = new TableOption({
      serviceProvider: {
        searchAsync: (request) => {
          return this.items(request);
        }
      },
      searchFields: ["name", "createdBy", "createdDate", "lastModifiedDate", "lastModifiedBy"],
      inlineEdit: true,
      mainColumns: [
        {
          title: () => this.headers[0],
          valueRef: () => 'src',
          type: TableColumnType.Custom,
          customTemplate: () => this.imageTemplate
        },
        {
          title: () => this.headers[1],
          valueRef: () => 'name',
          type: TableColumnType.Custom,
          customTemplate: () => this.nameTemplate,
          allowSort: true
        },
        {
          title: () => this.headers[2],
          valueRef: () => 'size',
          type: TableColumnType.Custom,
          customTemplate: () => this.sizeTemplate
        },
        {
          title: () => this.headers[3],
          valueRef: () => 'createdDate',
          type: TableColumnType.Date,
          allowSort: true
        }
      ]
    });
  }
}
