import { Component, ContentChild, Input, Output, EventEmitter } from '@angular/core';
import { BehaviorSubject, Subscription } from 'rxjs';
import { ListItemDirective } from './list-item.directive';
import { finalize, take } from 'rxjs/operators';

@Component({
  selector: 'katana-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss']
})

export class ListComponent {
  @ContentChild(ListItemDirective, { static: true }) public listItemTemplate: ListItemDirective;
  @Input() public eventSelect: boolean = true;
  @Input() public model: any[];
  @Input() public direction: 'horizontal' | 'vertical' = 'vertical';
  @Input() public emptyListMessage: string = 'Khng c gi tr no trong danh sch';
  @Input() validationName: string;
  @Input() validationScope: string;
  @Input() public pageSize: number = 10;
  @Input() public searchFunction: Function;
  @Output() public itemSelected: EventEmitter<any> = new EventEmitter();
  public loading: boolean;
  public numberOfItemsFromEndBeforeFetchingMore: number = 3;
  public searchItems: any[] = [];
  public searchText$ = new BehaviorSubject<string>(null);
  private subscriptions: Subscription[] = [];
  private currentPage: number = 0;
  private selectedItems: any[] = [];

  public isSelected(item: any): boolean {
    if (this.eventSelect) {
      return this.selectedItems.indexOf(item) >= 0;
    }
  }

  public selectItem(item: any, index: any) {
    if (this.eventSelect) {
      this.model.forEach(s => {
        s.selected = false;
      });
      const indexObj = this.model.find(s => s.id === item.id);
      indexObj.selected = true;
    }
    this.itemSelected.emit(item);
  }

  public getSelectedItems() {
    return this.selectedItems;
  }

  public scroll(lastIndex: number) {
    if (this.loading) {
      return;
    }
    if (lastIndex + this.numberOfItemsFromEndBeforeFetchingMore >= this.model.length) {
      this.fetchMore();
    }
  }

  public fetchMore() {
    this.currentPage++;
    this.loading = true;
    const fetchMoreSubsription = this.searchFunction(this.searchText$.getValue(), 0, this.currentPage, this.pageSize)
      .pipe(take(1), finalize(() => this.loading = false))
      .subscribe((response: any) => {
        this.model = this.model.concat(response.items);
        this.loading = false;
      });
    this.subscriptions.push(fetchMoreSubsription);
  }
}
