import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import {
  AuthenticationLoginRequest,
  AuthenticationLoginResponse,
  UserViewModel,
  AuthenticationSearchRequest,
  AuthenticationSearchResponse,
  AuthenticationRetrieveRequest,
  AuthenticationRetrieveResponse,
  AuthenticationCreateRequest,
  AuthenticationCreateResponse,
  AuthenticationUpdateRequest,
  AuthenticationUpdateResponse,
  AuthenticationDeleteRequest,
  AuthenticationDeleteResponse
} from './auth.model';
import { CacheService } from '../shared/services/cache.service';
import { AuthConst } from './auth.const';
import { Router } from '@angular/router';
import { MockService } from '../shared/services/mock.service';
import { MenuTab } from '../shared/models/base.model';
import { KeyConst } from '../shared/constants/key.const';

@Injectable({ providedIn: 'root' })
export class AuthenticationService extends MockService {
  public currentUser: UserViewModel;
  protected menuTabs: MenuTab[] = [
    {
      role: 'SA',
      items: [
        {
          label: 'Dashboard',
          icon: 'fa fa-pie-chart ',
          children: [
            { state: 'dashboard', name: 'Dashboard', type: 'link', icon: 'fa fa-pie' }
          ]
        }
      ]
    }
  ];

  constructor(
    protected httpClient: HttpClient,
    private _cacheService: CacheService,
    private _router: Router
  ) {
    super();
    var user = <UserViewModel>this._cacheService.get(AuthConst.User);
    this._cacheService.set(AuthConst.User, user);
    this.currentUser = user;
  }

  public setApi(url: string): void {
    this._cacheService.set(KeyConst.AuthApi, url);
  }

  public getApi(): string {
    var url = this._cacheService.get(KeyConst.AuthApi);
    if (!url) url = 'v1/authentications';
    return url;
  }

  public search(request: AuthenticationSearchRequest): Observable<AuthenticationSearchResponse> {
    return this.verify(this.httpClient.get<AuthenticationSearchResponse>(`${this.getApi()}/search`, { params: request as any }), request.mockData)
  }

  public retrieve(request: AuthenticationRetrieveRequest): Observable<AuthenticationRetrieveResponse> {
    return this.verify(this.httpClient.get<AuthenticationRetrieveResponse>(`${this.getApi()}/${request.payload.userName}`), request.mockData);
  }

  public login(request: AuthenticationLoginRequest): Observable<AuthenticationLoginResponse> {
    if (request.mockData && !request.mockData.menuTabs || (<any[]>request.mockData.menuTabs).length == 0) request.mockData.menuTabs = this.menuTabs;
    return this.verify(this.httpClient.post<AuthenticationLoginResponse>(`${this.getApi()}/login`, request), request.mockData);
  }

  public create(request: AuthenticationCreateRequest): Observable<AuthenticationCreateResponse> {
    return this.verify(this.httpClient.post<AuthenticationCreateResponse>(`${this.getApi()}/`, request), request.mockData);
  }

  public update(request: AuthenticationUpdateRequest): Observable<AuthenticationUpdateResponse> {
    return this.verify(this.httpClient.put<AuthenticationUpdateResponse>(`${this.getApi()}/`, request), request.mockData);
  }

  public delete(request: AuthenticationDeleteRequest): Observable<AuthenticationDeleteResponse> {
    return this.verify(this.httpClient.delete<AuthenticationDeleteResponse>(`${this.getApi()}?ids=${request.ids}`), request.mockData);
  }

  public logout(path: string = '/', refresh: boolean = false): void {
    this._cacheService.remove(AuthConst.User);
    this._cacheService.remove(KeyConst.Menu);
    if (path) this._router.navigate([path]);
    if (refresh) window.location.href = window.location.href;
  }
}