import * as _ from 'lodash';
import { Injectable } from '@angular/core';
import { ObjectChange } from '../models/base.model';
import { ChangeType } from '../enums/base.enum';

@Injectable({ providedIn: 'root' })
export class UtilityService {
    public getField(valueRef: string, lowerCase?: boolean): string {
        if (!valueRef) {
            return '';
        }
        if (!lowerCase) {
            return valueRef[0].toString().toUpperCase() + valueRef.substring(1, valueRef.length);
        } else {
            return valueRef[0].toString().toLowerCase() + valueRef.substring(1, valueRef.length);
        }
    }

    public toPascalCase(text: string, lowerCase?: boolean): string {
        if (!text) {
            return '';
        }
        if (!lowerCase) {
            return text[0].toString().toUpperCase() + text.substring(1, text.length);
        } else {
            return text[0].toString().toLowerCase() + text.substring(1, text.length);
        }
    }

    public compareObjects(source: any, target: any): ObjectChange[] {
        let changes: ObjectChange[] = [];
        const sourceProperties = Object.getOwnPropertyNames(source);
        const targetProperties = Object.getOwnPropertyNames(target);
        const addedProperties = targetProperties.filter(x => sourceProperties.indexOf(x) < 0);
        const removedProperties = sourceProperties.filter(x => targetProperties.indexOf(x) < 0);
        const modifiedProperties = sourceProperties.filter(x => removedProperties.indexOf(x) < 0);

        for (let property of modifiedProperties) {
            if (!_.isEqual(source[property], target[property])) {
                changes.push(new ObjectChange({
                    propertyName: property,
                    changeType: ChangeType.Update,
                    oldValue: source[property],
                    newValue: target[property]
                }));
            }
        }

        changes.push(...addedProperties.map(x => new ObjectChange({
            propertyName: x,
            changeType: ChangeType.Add,
            oldValue: target[x],
            newValue: target[x]
        })));

        changes.push(...removedProperties.map(x => new ObjectChange({
            propertyName: x,
            changeType: ChangeType.Delete,
            oldValue: source[x],
            newValue: source[x]
        })));
        
        return changes;
    }
}