import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { SettingSearchResponse } from '../models';
import { HttpClient } from '@angular/common/http';
import { SearchBaseRequest } from '../models/base.model';
import { CacheService } from './cache.service';

@Injectable({ providedIn: 'root' })
export class SettingService {
    constructor(
        protected httpClient: HttpClient,
        private _cacheService: CacheService
    ) { }

    public search(request: SearchBaseRequest): Observable<SettingSearchResponse> {
        return this.httpClient.get<SettingSearchResponse>(`v1/settings/search`, { params: request as any });
    }

    public useMock(value?: boolean): boolean {
        if (value == true || value == false) {
            this._cacheService.set('mock', value);
            return value;
        }
        var currentValue = <boolean>this._cacheService.get('mock');
        if (!currentValue) return false;
        return currentValue;
    }
}
