import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import {
  SendNotificationRequest,
  SendNotificationResponse,
  RetrieveNotificationThreadRequest,
  RetrieveNotificationThreadResponse,
  SearchInboxNotificationsRequest,
  SearchInboxNotificationsResponse,
  RemoveNotificationDetailRequest,
  RemoveNotificationDetailResponse,
  RetrieveNotificationDetailRequest,
  RetrieveNotificationDetailResponse,
  MarkAllNotificationsReadRequest,
  MarkAllNotificationsReadResponse,
  NotificationDetail
} from '../models/notification.model';

@Injectable()
export abstract class BaseNotificationService {
  public abstract success(message: string, title?: string): void;
  public abstract error(message: string, title?: string): void;
  public abstract info(message: string, title?: string): void;
  public abstract warning(message: string, title?: string): void;
  public abstract send(request: SendNotificationRequest): Observable<SendNotificationResponse>;
  public abstract retrieveNotificationThread(request: RetrieveNotificationThreadRequest): Observable<RetrieveNotificationThreadResponse>;
  public abstract searchNotificationDetails(request: SearchInboxNotificationsRequest): Observable<SearchInboxNotificationsResponse>;
  public abstract removeNotificationDetail(request: RemoveNotificationDetailRequest): Observable<RemoveNotificationDetailResponse>;
  public abstract retrieveNotificationDetail(request: RetrieveNotificationDetailRequest): Observable<RetrieveNotificationDetailResponse>;
  public abstract markAllRead(request: MarkAllNotificationsReadRequest): Observable<MarkAllNotificationsReadResponse>;
  public abstract registerConnection();
  public abstract redirect(detail: NotificationDetail): void;
}
