import { Component, Input } from "@angular/core";
import { FormDirectorExtraItemDirective } from './form-director-extra-item.directive';

@Component({
    selector: 'katana-form-director',
    templateUrl: './form-director.component.html',
    styleUrls: ['./form-director.component.scss'],
})

export class FormDirectorComponent {
    @Input() public direction: 'horizontal' | 'vertical' = 'vertical';
    @Input() public alignVertical: boolean = true;
    @Input() public title: string;
    @Input() public description: string;
    @Input() extraLabelItem: FormDirectorExtraItemDirective;
}