import { Directive, HostBinding, Inject, Input, OnInit, OnDestroy } from '@angular/core';
import { AccordionDirective } from './accordion.directive';

@Directive({
  selector: '[katanaAccordionLink]'
})

export class AccordionLinkDirective implements OnInit, OnDestroy {
  @Input() public group: any;
  @HostBinding('class.pcoded-trigger')
  @Input()

  get open(): boolean {
    return this._open;
  }

  set open(value: boolean) {
    this._open = value;
    document.querySelector('.pcoded-inner-navbar').classList.toggle('scroll-sidebar');
    if (value) {
      this.nav.closeOtherLinks(this);
    }
  }

  protected _open: boolean;
  protected nav: AccordionDirective;

  constructor(@Inject(AccordionDirective) nav: AccordionDirective) {
    this.nav = nav;
  }

  public ngOnInit(): any {
    this.nav.addLink(this);
  }

  public ngOnDestroy(): any {
    this.nav.removeGroup(this);
  }

  public toggle(): any {
    document.querySelector('.pcoded-inner-navbar').classList.add('scroll-sidebar');
    this.open = !this.open;
  }
}
