import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ButtonComponent } from './button/button.component';
import { DropdownButtonsComponent } from './dropdown-buttons/dropdown-buttons.component';
import { DropdownButtonComponent } from './dropdown-buttons/dropdown-button.component';
import { BsDropdownModule } from 'ngx-bootstrap';

@NgModule({
  declarations: [ButtonComponent, DropdownButtonsComponent, DropdownButtonComponent],
  imports: [
    CommonModule,
    BsDropdownModule.forRoot()
  ],
  exports: [ButtonComponent, DropdownButtonsComponent, DropdownButtonComponent]
})

export class ButtonModule { }
