import { Component, ViewEncapsulation, ViewChild } from '@angular/core';
import { DomSanitizer, SafeHtml } from '@angular/platform-browser';
import { BsModalRef } from 'ngx-bootstrap';
import { TemplateViewModel } from '../../modal.model';
import { LoaderComponent } from '../../../loader/loader.component';
import { AggregatorService } from '../../../services/aggregator.service';
import { ValidationService } from '../../../validation/validation.service';
import { SummaryError } from '../../../validation/validation.model';

@Component({
    selector: 'katana-template',
    templateUrl: './template.component.html',
    styleUrls: ['./template.component.scss'],
    encapsulation: ViewEncapsulation.None
})

export class TemplateComponent {
    @ViewChild('loader', { static: false }) public loader: LoaderComponent;
    public data: TemplateViewModel;
    protected close: Function;
    protected key: string;

    constructor(
        private _sanitizationService: DomSanitizer,
        private _aggregatorService: AggregatorService,
        public _modalRef: BsModalRef) {
    }

    public bypassSecurityTrustHtml(html: string): SafeHtml {
        return this._sanitizationService.bypassSecurityTrustHtml(html);
    }

    public cancel() {
        if (this.data.cancelCallback) {
            this.data.cancelCallback(null, () => {
                this._modalRef.hide();
            }, this.loader.componentRef.instance);
        } else {
            this._modalRef.hide();
        }
        if (this.data.autoClose != false) {
            this._modalRef.hide();
        }
    }

    public accept(close) {
        this.close = close;
        this.setKey(() => {
            this.isValid();
        });
    }

    public show(): boolean {
        if (this.loader && this.loader.componentRef && this.loader.componentRef.instance) {
            return this.loader.componentRef.instance.show();
        } else {
            return false;
        }
    }

    public isValid(): boolean {
        if (this.loader && this.loader.componentRef && this.loader.componentRef.instance && this.loader.componentRef.instance.isValid) {
            return this.loader.componentRef.instance.isValid();
        } else {
            return false;
        }
    }

    public setKey(callback: () => any): void {
        if (this.loader && this.loader.componentRef && this.loader.componentRef.instance && this.loader.componentRef.instance.getValidator) {
            var validator = <ValidationService>this.loader.componentRef.instance.getValidator();
            this.key = this.data.validationKey;
            if (validator) validator.setKey(this.key);
            this._aggregatorService.subscribe(this.key, (errors: SummaryError[]) => {
                this.close();
                if (!errors || errors.length == 0) {
                    if (this.data.acceptCallback) {
                        this._modalRef.hide();
                        this.loader.componentRef.instance.callback(this.data.mode).subscribe((response) => {
                            this.data.acceptCallback(response, () => {
                                this._modalRef.hide();
                            }, this.loader.componentRef.instance);
                        });
                    } else {
                        this._modalRef.hide();
                    }
                    if (this.data.autoClose != false) {
                        this._modalRef.hide();
                    }
                }
            });
            if (callback) callback();
        }
    }
}
