import { NgModule, ModuleWithProviders, Injectable } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { OwlDateTimeModule, OwlNativeDateTimeModule, OWL_DATE_TIME_LOCALE, OwlDateTimeIntl } from 'ng-pick-datetime';
import { DatetimePickerComponent } from './datetime/datetime.component';
import { DaterangePickerComponent } from './daterange/daterange.component';

const declarations = [DatetimePickerComponent, DaterangePickerComponent];

@NgModule({
    declarations: declarations,
    imports: [
        CommonModule,
        FormsModule,
        OwlDateTimeModule,
        OwlNativeDateTimeModule
    ],
    entryComponents: declarations,
    exports: declarations
})

export class DatetimePickerModule {
    public static forRoot(): ModuleWithProviders {
        return {
            ngModule: DatetimePickerModule,
            providers: [
            ]
        };
    }

    public static forChild(): ModuleWithProviders {
        return {
            ngModule: DatetimePickerModule,
            providers: [
                { provide: OWL_DATE_TIME_LOCALE, useValue: 'vi' },
                { provide: OwlDateTimeIntl, useClass: DefaultDateTimeLabels }
            ]
        };
    }
}

@Injectable({ providedIn: 'root' })
export class DefaultDateTimeLabels extends OwlDateTimeIntl {
    upSecondLabel = 'Thm 1 giy';
    downSecondLabel = 'Bt 1 giy';
    upMinuteLabel = 'Thm 1 pht';
    downMinuteLabel = 'Bt 1 pht';
    upHourLabel = 'Thm 1 gi';
    downHourLabel = 'Bt 1 gi';
    prevMonthLabel = 'Thng trc';
    nextMonthLabel = 'Thng ti';
    prevYearLabel = 'Nm trc';
    nextYearLabel = 'Nm ti';
    prevMultiYearLabel = '21 nm trc';
    nextMultiYearLabel = '21 nm sau';
    switchToMonthViewLabel = 'Xem theo thng';
    switchToMultiYearViewLabel = 'Xem theo thng nm';
    cancelBtnLabel = 'Quay li';
    setBtnLabel = 'Chn';
    rangeFromLabel = 'T';
    rangeToLabel = 'n';
    hour12AMLabel = 'AM';
    hour12PMLabel = 'PM';
};