import { Component, OnInit, ContentChildren, QueryList, TemplateRef, Input, SimpleChanges } from '@angular/core';
import { FormItemDirective } from './form-item.directive';

@Component({
    selector: 'katana-form',
    templateUrl: './form.component.html',
    styleUrls: ['./form.component.scss']
})

export class FormComponent implements OnInit {
    @ContentChildren(FormItemDirective) public formItems: QueryList<TemplateRef<any>>;
    @Input() public column: 1 | 2 | 3 | 4 = 4;
    public smallWidth: number = 12;
    public mediumWidth: number = 4;
    public largeWidth: number = 3;

    ngOnInit(): void {
    }

    ngOnChanges(changes: SimpleChanges): void {
        if (changes.column && changes.column.firstChange) {
            const column = changes.column.currentValue;
            switch (column) {
                case 1: {
                    this.smallWidth = 12;
                    this.mediumWidth = 12;
                    this.largeWidth = 12;
                    break;
                }
                case 2: {
                    this.smallWidth = 12;
                    this.mediumWidth = 12;
                    this.largeWidth = 6;
                    break;
                }
                case 3: {
                    this.smallWidth = 12;
                    this.mediumWidth = 6;
                    this.largeWidth = 4;
                    break;
                }
                case 4: {
                    this.smallWidth = 12;
                    this.mediumWidth = 4;
                    this.largeWidth = 3;
                    break;
                }
            }
        }
    }
}
