import { Component, ViewEncapsulation, Output, EventEmitter, Input, OnInit } from "@angular/core";
import { Breadcrumb, MenuTab } from '../shared/models/base.model';
import { AggregatorService } from '../shared/services/aggregator.service';
import { KeyConst } from '../shared/constants/key.const';
import { ActionService } from '../shared/services/action.service';

@Component({
    selector: 'default-breadcrumbs',
    templateUrl: './breadcrumbs.component.html',
    styleUrls: ['./breadcrumbs.component.scss'],
    encapsulation: ViewEncapsulation.None
})

export class DefaultBreadcrumbsComponent implements OnInit {
    @Input() public items: Breadcrumb[];
    @Input() public menuTabs: MenuTab[];
    @Output() change: EventEmitter<Breadcrumb[]> = new EventEmitter();

    constructor(
        protected aggregatorService: AggregatorService,
        protected actionService: ActionService
    ) { }

    ngOnInit(): void {
        this.aggregatorService.subscribe(KeyConst.MenuChanged, (items: Breadcrumb[]) => {
            this.items = items;
        });
    }

    public selectItem(item: Breadcrumb): void {
        if (!this.items || this.items.length == 0) return;
        var index = this.items.findIndex(s => s.label == item.label);
        if (index == -1) return;

        var data = [];
        for (let i = 0; i < this.items.length; i++) {
            data.push(this.items[i]);
            if (this.items[i].label == item.label) break;
        }
        var child = this.actionService.retrieveChild(item.state, this.menuTabs);
        if (child) {
            data.push(child);
        }
        this.items = data;
        this.aggregatorService.publish(KeyConst.MenuChanged, this.items);
    }
}
