import { Component, Input } from '@angular/core';
import { trigger, state, style, AUTO_STYLE, transition, animate } from '@angular/animations';

@Component({
  selector: 'katana-card',
  templateUrl: './card.component.html',
  styleUrls: ['./card.component.scss'],
  animations: [trigger('cardToggle', [
    state('collapsed, void',
      style({
        overflow: 'hidden',
        height: '0px',
      })
    ),
    state('expanded',
      style({
        height: AUTO_STYLE,
      })
    ),
    transition('collapsed <=> expanded', [
      animate('400ms ease-in-out')
    ])
  ]), trigger('cardClose', [
    state('open', style({
      opacity: 1
    })),
    state('closed', style({
      opacity: 0,
      display: 'none'
    })),
    transition('open <=> closed', animate('400ms')),
  ])],
})

export class CardComponent {
  @Input() public headerContent: string;
  @Input() public title: string;
  @Input() public blockClass: string;
  @Input() public cardClass: string;
  @Input() public classHeader = false;
  public cardToggle = 'expanded';
  public cardClose = 'open';
  public fullCard: string;
  public fullCardIcon: string;
  public loadCard = false;
  public isCardToggled = false;
  public cardLoad: string;

  public toggleCard(event: any) {
    this.cardToggle = this.cardToggle === 'collapsed' ? 'expanded' : 'collapsed';
  }

  public closeCard(event: any) {
    this.cardClose = this.cardClose === 'closed' ? 'open' : 'closed';
  }

  public fullScreen(event: any) {
    this.fullCard = this.fullCard === 'full-card' ? '' : 'full-card';
    this.fullCardIcon = this.fullCardIcon === 'icofont-resize' ? '' : 'icofont-resize';
  }

  public cardRefresh(event: any) {
    this.loadCard = true;
    this.cardLoad = 'card-load';
    setTimeout(() => {
      this.cardLoad = '';
      this.loadCard = false;
    }, 3000);
  }
}
