import { Renderer2, Injectable, RendererFactory2, EventEmitter, Output } from '@angular/core';
import { ValidationOption, ClientValidator, ValidationConstant, SummaryError, ValidationRule, ChangedItem } from './validation.model';
import { Subscription, Observable, forkJoin, merge, of } from 'rxjs';
import { map, defaultIfEmpty, take } from 'rxjs/operators';
import { DataService } from '../services/data.service';
import { ValidationProvider } from './validation.provider';
import { ActionService } from '../services/action.service';
import { AggregatorService } from '../services/aggregator.service';

@Injectable({ providedIn: 'root' })
export class ValidationService {
    @Output() public onDestroy: EventEmitter<void> = new EventEmitter();
    private elements: Element[] = [];
    private validator: ClientValidator;
    private errClass: string = ValidationConstant.DefaultErrorClass;
    private styles: string = ValidationConstant.DefaultErrorStyles;
    private attributeName: string = ValidationConstant.AttributeName;
    private renderer: Renderer2;
    private relatedProviders: ValidationService[] = [];
    private subscriptions: Subscription = new Subscription();
    private virtualValidationOptions: ValidationOption[] = [];
    private changedItems: ChangedItem[] = [];
    private key: string;

    constructor(
        protected rendererFactory: RendererFactory2,
        protected validationProvider: ValidationProvider,
        private _dataService: DataService,
        private _actionService: ActionService,
        private _aggregatorService: AggregatorService
    ) {
        this.renderer = rendererFactory.createRenderer(null, null);
    }

    ngOnDestroy() {
        this.subscriptions.unsubscribe();
        this.onDestroy.emit();
    }

    public init(model: { validator: ClientValidator }): void {
        this.validator = model.validator;
        if (!this.validator.payloadRef) this.validator.payloadRef = () => { return {} };

        if (this.validator.relatedValidationProviders) {
            this.addRelatedProviders(this.validator.relatedValidationProviders);
        }

        this.validator.options.forEach(option => {
            if (!option.displayText) option.displayText = option.validationName;
            if (!option.payloadRef) option.payloadRef = this.validator.payloadRef;
            option.rules.forEach(action => {
                if (!action.id) action.id = this._dataService.newGuid();
                if (!action.errorMessage) {
                    switch (action.key) {
                        case ValidationConstant.Required: {
                            action.errorMessage = (element: Element, rowIndex: string) => {
                                const displayingRowIndex = (+rowIndex + 1).toString();
                                return `${option.displayText}${option.dynamic ? ' [' + displayingRowIndex + ']' : ''} ${this.validator.requiredMessage}`;
                            };
                            break;
                        }
                        default: {
                            action.errorMessage = () => `${option.displayText} ${this.validator.invalidMessage}`;
                            break;
                        }
                    }
                }
            });
        });
        this.virtualValidationOptions = this.validator.options.filter(x => x.dynamic);
        this.updateAsync();
    }

    public setKey(key: string): void {
        if (!this.key) this.key = key;
    }

    public updateAsync(relatedProvidersToRegister?: ValidationService[]): void {
        this._actionService.executeAsync(() => {
            if (relatedProvidersToRegister && relatedProvidersToRegister.length) {
                this.addRelatedProviders(relatedProvidersToRegister);
            }
            this.registerElements();
            this.registerEvents();
        });
    }

    public executeAsync(validCallback: (errors?: SummaryError[]) => any, invalidCallback?: (errors?: SummaryError[]) => any): Observable<boolean> {
        this.retrieveSummaryErrors().subscribe((response) => {
            if (!response || response.length == 0) {
                if (validCallback) validCallback(response);
                return of(true);
            } else {
                if (invalidCallback) invalidCallback(response);
            }
        });
        return of(true);
    }

    public isValid(show: boolean = true, focus: boolean = true): boolean {
        if (show) {
            this.retrieveSummaryErrors().subscribe((errors: SummaryError[]) => {
                this._aggregatorService.publish(this.key, errors);
                if (focus) {
                    if (errors && errors.length > 0) {
                        var focusElement = errors[0];
                        var el = <any>focusElement.element;
                        if (el) {
                            this._actionService.executeAsync(() => {
                                el.focus();
                            });
                        }
                    }
                }
            });
        }

        let valid = true;
        if (this.validator.options) {
            this.validator.options.forEach((option) => {
                const payload = this.validator.payloadRef();
                const fieldValue = option.valueResolver ? option.valueResolver(payload) : null;
                if (option.rules) {
                    option.rules.forEach((action) => {
                        if (action.isValid != true) {
                            if (action.isValid == false) {
                                valid = false;
                            } else {
                                if (fieldValue) valid = false;
                            }
                        }
                    });
                }
            });
        }
        return valid;
    }

    public handleErrors(callback?: (response: SummaryError[]) => void): void {
        this.retrieveSummaryErrors().subscribe((res) => {
            if (callback) callback(res);
        });
    }

    public commit(callback?: Function): Observable<boolean> {
        return this.retrieveSummaryErrors().pipe(
            map(errors => {
                if (callback) callback(errors);
                if (errors.length == 0) return true;
                return false;
            }), take(1));
    }

    public setElementError(element: Element, action: ValidationRule, option: ValidationOption): string {
        if (!element) return;
        this.renderer.removeClass(element, ValidationConstant.SuccessElementClass);
        this.renderer.addClass(element, option.errorElementClass);
        let errorElement = this.findErrorElement(element);
        if (!errorElement) {
            errorElement = this.renderer.createElement('ul');
            this.renderer.setAttribute(errorElement, ValidationConstant.ERROR_ELEMENT_ID, `${this._dataService.newGuid()}`);
            this.renderer.setAttribute(errorElement, 'style', this.styles);
            this.renderer.addClass(errorElement, this.errClass);
            this.renderer.addClass(errorElement, option.errorMessageClass);
            const parentElement = this.renderer.parentNode(element);
            this.renderer.appendChild(parentElement, errorElement);
        }

        const errorItemElementKey: string = `${action.id}`;
        let errorItemElement = this.findErrorItemElement(errorElement, errorItemElementKey);
        const dynamicSequenceId = this.findDynamicSequenceId(element);
        const errorMessage = action.errorMessage(element, dynamicSequenceId);

        if (!errorItemElement) {
            errorItemElement = this.renderer.createElement('li');
            this.renderer.setAttribute(errorItemElement, ValidationConstant.ERROR_ITEM_ELEMENT_ID, errorItemElementKey);
            this.renderer.appendChild(errorItemElement, this.renderer.createText(errorMessage));

            this.renderer.appendChild(errorElement, errorItemElement);
        }
        return errorMessage;
    }

    public clearErrorItemElement(element: any, action: ValidationRule) {
        let errorElement = this.findErrorElement(element);
        if (!errorElement) return;

        const errorItemElementKey: string = `${action.id}`;
        let errorItemElement = this.findErrorItemElement(errorElement, errorItemElementKey);
        if (!errorItemElement) return;
        this.renderer.removeChild(errorElement, errorItemElement);
    }

    public isDirty(callback?: (items: ChangedItem[]) => void): boolean {
        if (!this.changedItems || this.changedItems.length == 0) return false;
        var response = this.changedItems.filter(s => s.change);
        if (callback) callback(response);
        return response.length > 0;
    }

    public validateElement(element: any, option: ValidationOption, all: boolean = false): Observable<ValidationOption> {
        const payload = this.validator.payloadRef();
        const fieldValue = option.valueResolver ? option.valueResolver(payload) : null;
        const validatedActions$ = option.rules.map(action => {
            switch (action.key) {
                case ValidationConstant.Required: {
                    return this.validationProvider.required(fieldValue).pipe(map(isValid => {
                        action.isValid = isValid;
                        return action;
                    }));
                }
                case ValidationConstant.Email: {
                    return this.validationProvider.email(fieldValue).pipe(map(isValid => {
                        action.isValid = isValid;
                        return action;
                    }));
                }
                case ValidationConstant.Phone: {
                    return this.validationProvider.phone(fieldValue).pipe(map(isValid => {
                        action.isValid = isValid;
                        return action;
                    }));
                }
                case ValidationConstant.Custom: {
                    if (!action.execute) throw new Error('!action.execute');
                    if (!all && !action.required) {
                        if (!fieldValue) {
                            action.isValid = true;
                            return of(action);
                        } else {
                            var requiredRule = option.rules.find(s => s.key == ValidationConstant.Required);
                            if (requiredRule) {
                                requiredRule.isValid = true;
                            }
                        }
                    }
                    const sequenceId = this.findDynamicSequenceId(element);
                    return action.execute(fieldValue, payload, +sequenceId)
                        .pipe(map(response => {
                            if (response) {
                                action.isValid = response.status;
                                if (response.message) action.errorMessage = () => { return response.message; };
                            }
                            return action;
                        }));
                }
                default: throw new Error(`Unhandled action: ${action.key}`);
            }
        });

        return forkJoin(validatedActions$).pipe(
            take(1),
            map(validatedActions => {
                if (option.relevantFields) this.validateRelevantFields(option.relevantFields(payload));
                option.rules = validatedActions;
                return option;
            }),
        );
    }

    private validateRelevantFields(relevantFields?: string[]): void {
        if (!relevantFields || relevantFields.length == 0) return;
        if (relevantFields && relevantFields.length > 0) {
            relevantFields.forEach(item => {
                for (let i = 0; i < this.elements.length; i++) {
                    var attributes = this.elements[i].attributes;
                    if (attributes && attributes.length > 0) {
                        if (attributes[this.attributeName].value == item) {
                            var option = this.findElementOption(this.elements[i]);
                            if (option) {
                                this.validateElement(this.elements[i], option, true).pipe(
                                    map(newOption => option = newOption),
                                    map(option => this.syncErrorMessages(this.elements[i], option)),
                                ).subscribe();
                            }
                            break;
                        }
                    }
                }
            });
        }
    }

    private retrieveSummaryErrors(): Observable<SummaryError[]> {
        const errors$ = this.elements.map(element => {
            let validationOption = this.findElementOption(element);
            if (!validationOption) return;

            return this.validateElement(element, validationOption).pipe(
                map(newOption => validationOption = newOption),
                map(option => this.syncErrorMessages(element, option)),
            );
        });

        const errorBatch = forkJoin(errors$);
        const relatedErrors = <Observable<SummaryError[]>>this.relatedProviders.reduce((previous: Observable<SummaryError[]>, provider: ValidationService) => {
            const subErrors$ = provider.retrieveSummaryErrors();
            return merge(previous, subErrors$);
        }, of([]));

        return forkJoin(errorBatch, relatedErrors).pipe(
            defaultIfEmpty([[], []]),
            map(value => [...value[0], ...value[1]]),
            map(result => [].concat(result.filter(error => error))),
        );
    }

    private findElementOption(element: Element): ValidationOption | null {
        const validationIdAttribute = element.attributes[ValidationConstant.VALIDATION_ID];
        if (!validationIdAttribute) return null;

        const validationId = validationIdAttribute.value;
        if (!validationId) return null;
        return this.validator.options.find(option => option.validationId === validationId);
    }

    private findErrorItemElement(errorElement: any, key: string): any | null {
        const children = <Element[]>Array.from(errorElement.children);
        return children
            .filter(x => x.attributes[ValidationConstant.ERROR_ITEM_ELEMENT_ID])
            .find(x => x.attributes[ValidationConstant.ERROR_ITEM_ELEMENT_ID].value === key);
    }

    private findDynamicSequenceId(element: Element): string | null {
        const sequenceIdAttribute = element.attributes[ValidationConstant.ARRAY_SEQUENCE_ID];
        if (!sequenceIdAttribute) return null;

        const sequenceId = sequenceIdAttribute.value;
        if (!sequenceId) return null;
        return sequenceId;
    }

    private findErrorElement(element: Element): Element {
        if (element.parentElement && element.parentElement.children) {
            const slibings = <Element[]>Array.from(element.parentElement.children);
            let result = slibings.filter(x => x.attributes).find(x => x.attributes[ValidationConstant.ERROR_ELEMENT_ID]);
            return result;
        }
        return null;
    }

    private registerElements(): void {
        const nonDynamicOptions = this.validator.options.filter(x => !x.dynamic);
        const nonDynamicElements = nonDynamicOptions.reduce((previous: Element[], current: ValidationOption) => {
            let query = `*[${this.attributeName}="${current.validationName}"]`;
            if (this.validator.scope) {
                query += `[scope="${this.validator.scope}"]`;
            }
            const elements = <Element[]>Array.from(this.validator.formRef.nativeElement.querySelectorAll(query));
            elements.forEach(element => {
                const validationId = this._dataService.newGuid();
                this.renderer.setAttribute(element, ValidationConstant.VALIDATION_ID, validationId);
                current.validationId = validationId;
            });
            return previous.concat(elements);
        }, []);

        let generatedDynamicOptions = [];
        let dynamicElements = [];
        this.validator.options = this.validator.options.filter(x => !x.dynamic);
        this.virtualValidationOptions.forEach((current: ValidationOption) => {
            let query = `*[${this.attributeName}="${current.validationName}"]`;
            if (this.validator.scope) {
                query += `[scope="${this.validator.scope}"]`;
            }
            const elements = Array.from(this.validator.formRef.nativeElement.querySelectorAll(query));
            const clonedOptions = elements.map((element, index) => {
                const validationId = this._dataService.newGuid();
                this.renderer.setAttribute(element, ValidationConstant.VALIDATION_ID, validationId);
                this.renderer.setAttribute(element, ValidationConstant.ARRAY_SEQUENCE_ID, index.toString());
                return {
                    ...current,
                    validationId: validationId,
                    valueResolver: current.valueResolver
                        ? (payload: any) => current.valueResolver(payload, index)
                        : () => { return (<any>element).value; }
                };
            });
            dynamicElements.push(...elements);
            generatedDynamicOptions.push(...clonedOptions);
        });

        this.validator.options = nonDynamicOptions.concat(generatedDynamicOptions);
        this.elements = nonDynamicElements.concat(dynamicElements);

        if (this.elements) {
            this.changedItems = [];
            this.elements.forEach((element) => {
                const payload = this.validator.payloadRef();
                let elementOption = this.findElementOption(element);
                this.changedItems.push(new ChangedItem({
                    id: elementOption.validationId,
                    field: elementOption.validationName,
                    oldValue: elementOption.valueResolver ? elementOption.valueResolver(payload) : null
                }));
            });
        }
    }

    private registerEvents(): void {
        this.elements.filter(element => element.attributes[this.attributeName]).forEach((element: any) => {
            if (!element.focusoutListener) {
                element.focusoutListener = this.renderer.listen(element, 'focusout', () => {
                    this.handleBlurEvent(element);
                });
                element.focusoutListener = this.renderer.listen(element, 'change', ($event) => {
                    const payload = this.validator.payloadRef();
                    let elementOption = this.findElementOption(element);
                    var value = elementOption.valueResolver ? elementOption.valueResolver(payload) : null;
                    if (elementOption && elementOption.dirtyCheck) {
                        var currentItem = this.changedItems.find(s => s.id == elementOption.validationId);
                        if (!currentItem) {
                            this.changedItems.push(new ChangedItem({
                                id: elementOption.validationId,
                                field: elementOption.validationName,
                                value: value,
                                change: true
                            }));
                        } else {
                            currentItem.value = value;
                            if (currentItem.oldValue == undefined || currentItem.oldValue == '') {
                                if (value == undefined || value == '') {
                                    currentItem.change = false;
                                } else {
                                    currentItem.change = true;
                                }
                            } else {
                                if (value == undefined || value == '') {
                                    currentItem.change = true;
                                } else {
                                    if (currentItem.oldValue.toString() != currentItem.value.toString()) {
                                        currentItem.change = true;

                                    } else {
                                        currentItem.change = false;
                                    }
                                }
                            }
                        }
                    }
                    if (elementOption && elementOption.rules) {
                        elementOption.rules.forEach((rule) => {
                            rule.isValid = false;
                        });
                    }
                });
            }
        });
    }

    private handleBlurEvent(element: any): void {
        let elementOption = this.findElementOption(element);
        if (!elementOption) throw new Error('!elementOption');

        this.validateElement(element, elementOption).pipe(take(1)).subscribe(newOption => {
            elementOption = newOption;
            this.syncErrorMessages(element, elementOption);
        });
    }

    private syncErrorMessages(element: any, option: ValidationOption): SummaryError | null {
        let errorMessages: string[] = [];
        option.rules.forEach(action => {
            if (action.isValid) {
                this.clearErrorItemElement(element, action);
            } else {
                const errorMessage = this.setElementError(element, action, option);
                errorMessages.push(errorMessage);
            }
        });

        const invalidActions = option.rules.filter(x => !x.isValid);
        if (invalidActions.length === 0) {
            this.renderer.removeClass(element, option.errorElementClass);
            this.renderer.removeClass(element, option.successElementClass);
            return null;
        }

        return {
            element: element,
            messages: errorMessages
        }
    }

    private addRelatedProviders(providers: ValidationService[]): void {
        let newProviders = providers.filter(x => this.relatedProviders.indexOf(x) < 0);
        newProviders.forEach(provider =>
            provider.onDestroy.subscribe(() => {
                this.relatedProviders = this.relatedProviders.filter(() => this.relatedProviders.indexOf(provider) < 0);
            }));
        this.relatedProviders.push(...newProviders);
    }
}