import { Component, ContentChild, Input } from '@angular/core';
import { trigger, state, style, AUTO_STYLE, transition, animate } from '@angular/animations';
import { PanelHeaderDirective } from './panel-header.directive';

@Component({
  selector: 'katana-panel',
  templateUrl: './panel.component.html',
  styleUrls: ['./panel.component.scss'],
  animations: [
    trigger('expand', [
      state('false',
        style({
          overflow: 'hidden',
          height: '0px',
        })
      ),
      state('true',
        style({
          height: AUTO_STYLE,
        })
      ),
      transition('0 <=> 1', [
        animate('300ms ease-in-out')
      ])
    ]),
  ]
})

export class PanelComponent {
  @ContentChild(PanelHeaderDirective, { static: true }) public panelHeader: PanelHeaderDirective;
  @Input() public title: string;
  @Input() public icon: string;
  @Input() public includeBorder: boolean = true;
  @Input() public expand: boolean = true;
  @Input() public disabled: boolean;

  public toggle() {
    this.expand = !this.expand;
  }
}
