import { Component, Input, Output, EventEmitter, ContentChildren } from '@angular/core';
import { DropdownButtonComponent } from './dropdown-button.component';

@Component({
  selector: 'katana-dropdown-buttons',
  templateUrl: './dropdown-buttons.component.html',
  styleUrls: ['./dropdown-buttons.component.scss']
})

export class DropdownButtonsComponent {
  @Input() public title: string;
  @Input() public customClass: string;
  @Input() public disabled: boolean = false;
  @Input() public icon: string;
  @Input() public lazyload: boolean = false;
  @Input() public loadingIcon: string = 'fa fa-spinner icon-loader';
  @Output() public execute: EventEmitter<any> = new EventEmitter();
  @ContentChildren(DropdownButtonComponent) public items: DropdownButtonComponent[];
  public loading: boolean;

  public handleAction($event) {
    if (this.loading) {
      $event.preventdefault();
      return;
    };

    if (this.lazyload) {
      this.loading = true;
      this.execute.emit(() => { this.loading = false; });
    } else {
      this.execute.emit();
    }
  }
}
