import { Injectable } from '@angular/core';
import { FileService } from './file.service';
import { ModalService } from '../modals';
import { FileViewModel, FileRequest } from './file.model';
import { FileConst } from './file.const';
import { AuthenticationService } from '../../auth/auth.service';
import { ActionService } from '../services/action.service';

@Injectable({ providedIn: 'root' })
export class FileProvider {
    constructor(
        private _fileService: FileService,
        private _modalService: ModalService,
        private _actionService: ActionService,
        private _authenticationService: AuthenticationService
    ) { }

    public uploadAsync(files: File[], attachments: FileViewModel[], fileRef: any, callback?: () => any): void {
        var request = this.buildRequest(files, attachments);
        this._actionService.executeAsync(() => {
            const totalAttachmentSize = this.calculateTotalBytes(attachments, request.items);
            if (totalAttachmentSize > FileConst.MaxSize * 1024 * 1024) {
                this._modalService.showNotificationDialog({
                    autoClose: true,
                    title: 'Thng bo',
                    message: 'Dung lng khng c vt qu <span class="text-bold text-primary">' + FileConst.MaxSize + '</span> MB.',
                    btnTitle: 'ng'
                });
                if (callback) {
                    callback();
                }
            } else {
                if (request.others.length == 0) {
                    this.uploadFileAsync(request, fileRef, callback);
                } else {
                    let otherTypes = '';
                    request.others.forEach(file => {
                        if (file) {
                            if (otherTypes.indexOf(file.type) == -1) {
                                otherTypes += file.type + '|';
                            }
                        }
                    });
                    if (otherTypes) otherTypes = otherTypes.substring(0, otherTypes.length - 1);
                    if (request.items.length == 0) {
                        this._modalService.showNotificationDialog({
                            autoClose: true,
                            title: 'Thng bo',
                            message: 'nh dng tp tin khng hp l <span class="text-bold text-primary">' + otherTypes + '</span>.',
                            btnTitle: 'ng'
                        });
                        if (callback) {
                            callback();
                        }
                    } else {
                        this._modalService.showConfirmDialog({
                            autoClose: true,
                            title: 'Thng bo',
                            message: 'C <span class="text-bold text-primary">' + request.others.length + '</span> nh dng tp tin khng hp l <span class="text-bold text-primary">' + otherTypes + '</span>.<br/> Bn c mun tip tc ti tp tin khc khng?',
                            btnAcceptTitle: 'Tip tc',
                            btnCancelTitle: 'Hy',
                            acceptCallback: () => {
                                this.uploadFileAsync(request, fileRef, callback);
                            }
                        });
                        if (callback) {
                            callback();
                        }
                    }
                }
            }
        });
    }

    private uploadFileAsync(request: FileRequest, fileRef: any, callback?: () => any): void {
        this._fileService.uploadFiles(request).subscribe(response => {
            if (fileRef && fileRef.value) {
                fileRef.value = '';
            }

            if (response && response.items) {
                if (callback) {
                    callback();
                }

                response.items.forEach((item) => {
                    const currentAttachment = request.items.find(s => s.src == item.src && s.name == item.name);
                    if (!currentAttachment) {
                        request.items.push(item);
                    }
                });
            }
        });
    }

    private calculateTotalBytes(attachments: FileViewModel[], items?: FileViewModel[]): number {
        let totalAmount = 0;
        if (attachments) {
            if (attachments) {
                attachments.forEach((attachment) => {
                    totalAmount += attachment.size;
                });
            }

            if (items) {
                items.forEach((attachment) => {
                    totalAmount += attachment.size;
                });
            }
        }
        return totalAmount;
    }

    private buildRequest(files: File[], attachments: FileViewModel[]): FileRequest {
        const request: FileRequest = {
            createdBy: this._authenticationService.currentUser.id,
            items: []
        }

        const otherFiles: FileViewModel[] = [];

        for (let i = 0; i < files.length; i++) {
            if (files[i]) {
                const reader = new FileReader();
                reader.readAsDataURL(files[i]);
                reader.onloadend = () => {
                    const base64String = <string>reader.result;
                    if (files[i].type && FileConst.AcceptedFiles.indexOf(files[i].type) > -1) {
                        request.items.push(
                            {
                                size: files[i].size,
                                type: files[i].type,
                                src: base64String,
                                name: files[i].name
                            });
                    } else {
                        otherFiles.push(
                            {
                                size: files[i].size,
                                type: files[i].type,
                                src: base64String,
                                name: files[i].name
                            });
                    }
                }
            }
        }

        if (attachments) {
            attachments.forEach((item) => {
                const currentAttachment = request.items.find(s => s.src == item.src && s.name == item.name);
                if (!currentAttachment) {
                    request.items.push(item);
                }
            });
        }

        request.others = otherFiles;
        return request;
    }
}
