import { Component, ViewEncapsulation } from '@angular/core';
import { DomSanitizer, SafeHtml } from '@angular/platform-browser';
import { BsModalRef } from 'ngx-bootstrap';
import { ConfirmViewModel } from '../../modal.model';

@Component({
    selector: 'katana-confirm',
    templateUrl: './confirm.component.html',
    styleUrls: ['./confirm.component.scss'],
    encapsulation: ViewEncapsulation.None
})

export class ConfirmComponent {
    public data: ConfirmViewModel;
    constructor(
        private _sanitizationService: DomSanitizer,
        public _modalRef: BsModalRef) {
    }

    public bypassSecurityTrustHtml(html: string): SafeHtml {
        return this._sanitizationService.bypassSecurityTrustHtml(html);
    }

    public cancel() {
        if (this.data.cancelCallback) {
            this.data.cancelCallback(() => {
                this._modalRef.hide();
            });
        } else {
            this._modalRef.hide();
        }
        if (this.data.autoClose != false) {
            this._modalRef.hide();
        }
    }

    public accept() {
        if (this.data.acceptCallback) {
            this.data.acceptCallback(() => {
                this._modalRef.hide();
            });
        } else {
            this._modalRef.hide();
        }
        if (this.data.autoClose != false) {
            this._modalRef.hide();
        }
    }
}
