import { Component, OnInit, ContentChildren, Output, EventEmitter, Input, ChangeDetectorRef } from '@angular/core';
import { TabItemComponent } from './tab-item.component';

@Component({
  selector: 'katana-tabs',
  templateUrl: './tab.component.html',
  styleUrls: ['./tab.component.scss']
})

export class TabComponent {
  @ContentChildren(TabItemComponent) public tabs: TabItemComponent[];
  @Output() public change: EventEmitter<TabItemComponent> = new EventEmitter();
  @Input() public displayMode: 'full' | 'default' = 'default';

  public activeTabIndex: number = 0;
  public clicked: boolean = false;

  constructor(
    private _changeDetectorRef: ChangeDetectorRef
  ) { }

  public switchTab(tabIndex: number, tab: TabItemComponent) {
    if (this.activeTabIndex === tabIndex) return;
    this._changeDetectorRef.detectChanges();
    this.activeTabIndex = tabIndex;
    tab.index = tabIndex;
    this.change.emit(tab);
  }
}
