import { Component, Input, ViewChild, OnInit } from "@angular/core";
import { Observable, of } from 'rxjs';
import { ImageCropperComponent, ImageCroppedEvent } from "ngx-image-cropper";
import { ActionService } from '../services/action.service';

@Component({
    selector: 'katana-cropper',
    templateUrl: './cropper.component.html',
    styleUrls: ['./cropper.component.scss']
})

export class CropperComponent implements OnInit {
    @ViewChild('cropper', { static: true }) public cropper: ImageCropperComponent;
    @Input() public imageElement: HTMLImageElement;
    @Input() public eventFile: any;
    @Input() public cutRatio: number = 1 / 1;
    @Input() public resizeToWidth: number = 800;
    @Input() public maintainAspectRatio: boolean = true;
    imageChangedEvent: any = '';
    croppedImage: any = '';
    showCropper = false;

    constructor(
        private _actionService: ActionService
    ) { }

    ngOnInit(): void {
        this.init();
    }

    public setImage(image: any) {
        this.imageChangedEvent = this.eventFile;
    }

    public imageLoaded() {
        this.showCropper = true;
        console.log('Image loaded');
    }

    public cropperReady() {
        console.log('Cropper ready');
    }

    public loadImageFailed() {
        console.log('Load failed');
    }

    public imageCropped(event: ImageCroppedEvent) {
        this.croppedImage = event.base64;
    }

    public isValid(): boolean {
        return true;
    }

    public callback(): Observable<any> {
        return of(this.croppedImage);
    }

    public getErrors() {
        return of([]);
    }

    private init(): void {
        this._actionService.executeAsync(() => {
            this.imageChangedEvent = this.eventFile;
        });
    }
}
