import { FileService } from '../file/file.service';
import { FileRequest, FileViewModel, FileResponse } from '../file/file.model';
import { AuthenticationService } from '../../auth/auth.service';

export class EditorAdapter {
    protected loader: any;
    protected fileService: FileService;
    protected authenticationService: AuthenticationService;

    constructor(loader: any, fileService: FileService, authenticationService: AuthenticationService) {
        this.loader = loader;
        this.fileService = fileService;
        this.authenticationService = authenticationService;
    }

    public upload(): any {
        return this.loader.file.then(file => new Promise((resolve) => {
            var reader = new FileReader();
            reader.onloadend = () => {
                const base64Data = reader.result.toString().split(',');
                if (base64Data.length < 2) {
                    return;
                }
                const base64FileData = base64Data[1];
                this.fileService.uploadFiles(new FileRequest({
                    items: [
                        new FileViewModel({
                            name: file.name,
                            src: base64FileData
                        })
                    ],
                    mockData: new FileResponse({
                        items: [new FileViewModel({
                            src: <string>reader.result,
                            name: file.name
                        })]
                    })
                })).subscribe(response => {
                    var src = '';
                    if (response.items && response.items.length > 0) {
                        src = response.items[0].src;
                    }
                    resolve({ default: src });
                }, (err) => {
                    console.log(err);
                });
            }
            reader.readAsDataURL(file);
        }));
    }
}