import { KeyValueItem } from './base.model';

export interface BaseSearchRequest {
  searchText: string;
  orderBy: string;
  direction: string;
  createdBy: string;
  pageIndex: number;
  pageSize: number;
}

export interface BaseSearchResponse<T> {
  items: T[];
  totalRecords: number;
}

export class GetLookupItemsRequest {
  public params: string;
  constructor(init?: Partial<GetLookupItemsRequest>) {
    Object.assign(this, init);
  }
}

export class GetLookupItemsResponse {
  message: string;
  success: boolean;
  id: string;
  item: any;
  constructor(init?: Partial<GetLookupItemsResponse>) {
    Object.assign(this, init);
  }
}

export class SendNotificationRequest {
  notificationTypes: string[] = [];
  payloadItems: KeyValueItem[] = [];
  templateCode: string;
  recipients: string[] = [];
  fromAbiding: boolean;
  constructor(init?: Partial<SendNotificationRequest>) {
    Object.assign(this, init);
  }
}

export class SendNotificationResponse {
  message: string;
  success: boolean;
  id: string;
  constructor(init?: Partial<SendNotificationResponse>) {
    Object.assign(this, init);
  }
}

export class NotificationType {
  public static Email: string = 'Email';
  public static Sms: string = 'Sms';
  public static Local: string = 'Local';
}

export class EditNotificationThreadViewModel {
  id: string;
  hasRead: boolean;
  detailGroups: NotificationDetailGroup[] = [];
  constructor(init?: Partial<EditNotificationThreadViewModel>) {
    Object.assign(this, init);
  }
}

export class NotificationDetailGroup {
  details: NotificationDetail[] = [];
  date: Date;
  constructor(init?: Partial<NotificationDetailGroup>) {
    Object.assign(this, init);
  }
}

export class NotificationDetail {
  description: string;
  title: string;
  time: Date;
  code: string;
  id: string;
  hasRead: boolean;
  payload: any;
  constructor(init?: Partial<NotificationDetail>) {
    Object.assign(this, init);
  }
}

export class RetrieveNotificationThreadRequest {
  id: string;
  pageIndex: number;
  pageSize: number;
  constructor(init?: Partial<RetrieveNotificationThreadRequest>) {
    Object.assign(this, init);
  }
}

export class RetrieveNotificationThreadResponse {
  message: string;
  item: EditNotificationThreadViewModel;
  success: boolean;
  id: string;
  constructor(init?: Partial<RetrieveNotificationThreadResponse>) {
    Object.assign(this, init);
  }
}

export class NotificationTemplateVariable {
  public static OrderIdentifier: string = 'OrderIdentifier';
  public static PendingApprovalProductsCount: string = 'PendingApprovalProductsCount';
  public static SuppliersCount: string = 'SuppliersCount';
  public static PendingInquiriesCount: string = 'PendingInquiriesCount';
  public static BuyerName: string = 'BuyerName';
  public static ProductName: string = 'ProductName';
}

export class RegisterChannelRequest {
  constructor(init?: Partial<RegisterChannelRequest>) {
    Object.assign(this, init);
  }
}

export class RegisterChannelResponse {
  message: string;
  id: string; success: boolean;
  constructor(init?: Partial<RegisterChannelResponse>) {
    Object.assign(this, init);
  }
}

export class SendLiveNotificationRequest {
  message: string;
  recipients: string[] = [];
  constructor(init?: Partial<SendLiveNotificationRequest>) {
    Object.assign(this, init);
  }
}

export class RemoveNotificationDetailRequest {
  id: string;
  groupType: string;
  dateGroup: Date;
  details: string[];
  constructor(init?: Partial<RemoveNotificationDetailRequest>) {
    Object.assign(this, init);
  }
}

export class RemoveNotificationDetailResponse {
  message: string;
  success: boolean;
  id: string;
  constructor(init?: Partial<RemoveNotificationDetailResponse>) {
    Object.assign(this, init);
  }
}

export class RemoveNotificationDetailGroupType {
  public static All: string = 'All';
  public static GroupByDate: string = 'GroupByDate';
  public static Single: string = 'Single';
}

export class RetrieveNotificationDetailRequest {
  id: string;
  userId: string;
  constructor(init?: Partial<RetrieveNotificationDetailRequest>) {
    Object.assign(this, init);
  }
}

export class RetrieveNotificationDetailResponse {
  message: string;
  item: NotificationDetail;
  success: boolean;
  id: string;
  constructor(init?: Partial<RetrieveNotificationDetailResponse>) {
    Object.assign(this, init);
  }
}

export class SearchInboxNotificationsRequest implements BaseSearchRequest {
  pageIndex: number;
  pageSize: number;
  searchText: string;
  orderBy: string;
  direction: string;
  createdBy: string;
  userId: string;
  fromAbiding: boolean;
  constructor(init?: Partial<SearchInboxNotificationsRequest>) {
    Object.assign(this, init);
  }
}

export class SearchInboxNotificationsResponse implements BaseSearchResponse<NotificationDetailGroup> {
  message: string;
  items: NotificationDetailGroup[];
  totalPendingRecords: number;
  totalRecords: number;
  success: boolean;
  id: string;
  constructor(init?: Partial<SearchInboxNotificationsResponse>) {
    Object.assign(this, init);
  }
}

