import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'katana-button',
  templateUrl: './button.component.html',
  styleUrls: ['./button.component.scss']
})

export class ButtonComponent implements OnInit {
  @Input() public title: string;
  @Input() public customClass: 'default' | 'success' | 'warning' | 'danger' | 'link' | 'info' | 'inverse' | 'primary' | 'outline-primary' | 'outline-inverse' = 'default';
  @Input() public disabled: boolean = false;
  @Input() public icon: string;
  @Input() public loadingIcon: string = 'fa fa-spinner icon-loader';
  @Input() public lazyload: boolean = false;
  @Input() public tooltip: string;
  @Output() public execute: EventEmitter<any> = new EventEmitter();
  public loading: boolean = false;

  ngOnInit() { }

  public handleAction($event): void {
    if (this.loading) {
      $event.preventdefault();
      return;
    };

    if (this.lazyload) {
      this.loading = true;
      this.execute.emit(() => { this.loading = false; });
    } else {
      this.execute.emit();
    }
  }

  public retrieveButtonClass(): string {
    if (this.customClass) return `btn-${this.customClass}`;
    return 'btn-default';
  }
}
