import { Component, ViewChild, ViewContainerRef, TemplateRef } from "@angular/core";
import { DefaultLayoutService } from './layout.service';
import { AggregatorService } from '../shared/services/aggregator.service';
import { ButtonDefinition, ToolbarActionPayload } from '../shared/models/base.model';
import { ToolbarAction } from '../shared/enums/base.enum';
import { SummaryError } from '../shared/validation/validation.model';
import { KeyConst } from '../shared';

@Component({
    selector: 'default-toolbar',
    templateUrl: './toolbar.component.html'
})

export class DefaultToolbarComponent {
    @ViewChild('errorContainer', { read: ViewContainerRef, static: true }) public errorContainer: ViewContainerRef;
    @ViewChild('errorTemplate', { static: true }) public errorTemplate: TemplateRef<any>;

    constructor(
        public layoutService: DefaultLayoutService,
        private _aggregatorService: AggregatorService
    ) {
    }

    ngOnInit() {
        this._aggregatorService.subscribe(KeyConst.PageChanged, () => {
            if (this.errorContainer) {
                this.errorContainer.clear();
            }
        });
    }

    public onButtonClicked(loadedCallback: Function, item: ButtonDefinition) {
        let payload = new ToolbarActionPayload({ action: item.action, loadedCallback: loadedCallback });
        if (item.action === ToolbarAction.Save) {
            payload.callback = (errors: SummaryError[]) => {
                this.errorContainer.clear();
                this.errorContainer.createEmbeddedView(this.errorTemplate, { errors: errors });
            };
        }
        this._aggregatorService.publish('ToolbarButtonClicked', payload);
    }
}
