import { Injectable, EventEmitter } from '@angular/core';
import { AggregatorViewModel } from '../models/base.model';

@Injectable({ providedIn: 'root' })
export class AggregatorService {
    protected emitters: AggregatorViewModel[] = [];

    public publish<T>(key: string, value?: T): void {
        if (!key) return;
        let currentEmitter = <AggregatorViewModel>this.emitters.find(s => s.key == key);
        if (!currentEmitter) {
            currentEmitter = new AggregatorViewModel({
                value: this.register(key)
            });
        }
        currentEmitter.value.emit(value);
    }

    public subscribe<T>(key: string, callback: (response: T) => void): void {
        if (!key) return;
        let index = this.emitters.findIndex(s => s.key == key);
        if (index > -1) {
            this.emitters.splice(index, 1);
        }
        var currentEmitter = new AggregatorViewModel({
            value: this.register(key)
        });
        currentEmitter.value.subscribe((response: T) => {
            callback(response);
        });
    }

    public hasKey(key: string): boolean {
        return this.emitters.findIndex(s => s.key == key) > -1;
    }

    private register(key: string): EventEmitter<any> {
        var emitter = new AggregatorViewModel({
            key: key,
            value: new EventEmitter<any>()
        });
        this.emitters.push(emitter);
        return emitter.value;
    }
}
