import { Pipe, PipeTransform } from '@angular/core';
import { DecimalPipe } from '@angular/common';

@Pipe({
  name: 'katanaFile'
})

export class FilePipe extends DecimalPipe implements PipeTransform {
  transform(value: string, digits?: string, locale?: string): string | null {
    if (!value) return '';
    var name = value.toString();
    var data = name.split('.');
    if (data.length == 1) return `${data[0]}`;
    var index = name.indexOf(data[data.length - 1]);
    return `${name.substring(0, index - 1)} ${data[data.length - 1]}`;
  }
}
