import { Component, OnInit, OnDestroy, ContentChild, TemplateRef, Input, Output, EventEmitter, ChangeDetectorRef } from '@angular/core';
import { Observable, BehaviorSubject, Subscription } from 'rxjs';
import { debounceTime, distinctUntilChanged, tap, switchMap, map, concat, skip } from 'rxjs/operators';
import { FormDirectorExtraItemDirective } from '../form/form-director-extra-item.directive';

@Component({
    selector: 'katana-dropdown',
    templateUrl: './dropdown.component.html',
    styleUrls: ['./dropdown.component.scss']
})

export class DropdownComponent implements OnInit, OnDestroy {
    @ContentChild('itemTemplate', { static: true }) public itemTemplate: TemplateRef<any>;
    @ContentChild('groupTemplate', { static: true }) public groupTemplate: TemplateRef<any>;
    @ContentChild('footerTemplate', { static: true }) public footerTemplate: TemplateRef<any>;
    @ContentChild(FormDirectorExtraItemDirective, { static: true }) extraLabelItem: FormDirectorExtraItemDirective;
    @Output() public change = new EventEmitter<any>();
    @Input() public title: string;
    @Input() public description: string;
    @Input() public direction: 'horizontal' | 'vertical' = 'vertical';
    @Input() public alignVertical: boolean = true;
    @Input() public multiple: boolean = false;
    @Input() public bindLabel: string;
    @Input() public bindValue: string;
    @Input() public model: any;
    @Input() public closeOnSelect: boolean = true;
    @Input() public dropdownPosition: string = 'auto';
    @Input() public loading: boolean;
    @Input() public loadingText: string = 'ang ti...';
    @Input() public markFirst: boolean;
    @Input() public notFoundText: string = 'Khng tm thy';
    @Input() public placeholder: string = 'Chn thng tin';
    @Input() public pageSize: number = 10;
    @Input() public groupBy: string;
    @Input() public readonly: boolean = false;
    @Input() public disabled: boolean = false;
    @Input() public typeToSearchText: string = 'G  tm kim';
    @Input() public searchable: boolean = true;
    @Input() public searchFunction: (searchText: string, index: number, pageIndex: number, pageSize: number) => Observable<{ items: any[], totalItems: number }>;
    @Input() public lazyload: boolean = false;
    @Input() public beforeSearch: Observable<any>;
    @Input() public afterSearch: Observable<any>;
    @Input() public validationName: string;
    @Input() public validationScope: string;
    @Input() public controlName: string;
    @Input() emitNullOnDestroy: boolean = false;
    public totalItems: number = 0;
    public searchItems: any[] = [];
    public searchText$ = new BehaviorSubject<string>(null);
    private subscriptions: Subscription = new Subscription();
    private currentPage: number = 0;
    private numberOfItemsFromEndBeforeFetchingMore: number = 3;
    private fetchMore$: BehaviorSubject<number> = new BehaviorSubject(0);

    constructor(
        private changeDetectorRef: ChangeDetectorRef
    ) { }

    ngOnInit(): void {
        const search$ = this.searchText$
            .pipe(
                debounceTime(200),
                distinctUntilChanged(),
                tap(() => this.loading = true),
                switchMap((text: string) => {
                    this.currentPage = 0;
                    return this.searchFunction(text, 0, this.currentPage, this.lazyload ? this.pageSize : null);
                }),
                map((response: any) => this.rebind(response)),
                tap(() => this.loading = false),
            );

        const fetchMore$ = this.fetchMore$
            .pipe(
                tap(() => this.loading = true),
                switchMap((pageIndex: number) => {
                    return this.searchFunction(this.searchText$.getValue(), 0, pageIndex, this.lazyload ? this.pageSize : null);
                }),
                map((response: any) => this.append(response)),
                tap(() => this.loading = false),
            );
        this.subscriptions.add(fetchMore$.subscribe());

        if (this.beforeSearch) {
            this.subscriptions.add(this.beforeSearch.pipe(concat(search$)).subscribe());
        } else {
            this.subscriptions.add(search$.subscribe());
        }

        if (this.afterSearch) {
            const afterSearchSubscription = this.afterSearch.pipe(skip(1)).subscribe(response => this.rebind(response));
            this.subscriptions.add(afterSearchSubscription);
        }
    }

    ngOnDestroy(): void {
        this.subscriptions.unsubscribe();
        if (this.emitNullOnDestroy === true)
            this.change.emit(null);
    }

    public onBlur() {
        this.searchText$.next(null);
    }

    public scroll(lastIndex: number) {
        if (this.loading) {
            return;
        }

        if (lastIndex + this.numberOfItemsFromEndBeforeFetchingMore >= this.searchItems.length) {
            this.fetchMore();
        }
    }

    public fetchMore() {
        this.currentPage++;
        this.fetchMore$.next(this.currentPage);
    }

    public clear() {
        if (this.lazyload) {
            this.currentPage = 0;
            this.fetchMore$.next(this.currentPage);
        }
    }

    private rebind(response: any) {
        this.searchItems = response.items;
        this.totalItems = response.totalRecords;
        this.changeDetectorRef.markForCheck();
    }

    private append(response: any) {
        this.searchItems = this.searchItems.concat(response.items);
        this.totalItems = response.totalRecords;
        this.changeDetectorRef.markForCheck();
    }
}
