import { Observable } from 'rxjs';
import { TableComponent } from './table.component';
import { TemplateRef, ElementRef, Type } from '@angular/core';

export interface TableEditInline {
  enabled?: boolean;
  autoCommit?: boolean;
  createAsync?: (item: any) => Observable<any>;
  updateAsync?: (item: any, column?: TableColumn) => Observable<any>;
}

export interface TableCell {
  item?: any;
  column?: TableColumn;
}

export class TableColumn {
  title?: () => string;
  valueRef?: () => any;
  direction?: string;
  allowSort?: boolean;
  allowFilter?: boolean;
  order?: number;
  customClass?: string;
  defaultSorter?: boolean;
  width?: number;
  textAlign?: string;
  type?: TableColumnType;
  showTooltip?: boolean;
  editInline?: boolean;
  callback?: (provider?: TableComponent, element?: ElementRef, $event?: any) => any;
  customTemplate?: () => TemplateRef<any>;
  hide?: () => boolean;
  dropdownConfiguration?: {
    searchFunction: (text: string, index: number, currentPage: number, pageSize: number) => Observable<{ items: any[], totalRecords: number }>,
    multiple?: boolean,
    bindLabel: string,
    bindValue: string,
  };
  id?: string;
  filterTemplate?: () => TemplateRef<any>;

  constructor(init?: Partial<TableColumn>) {
    Object.assign(this, init);
  }
}

export class TableSorter {
  direction?: string;
  orderBy?: string;
  order?: number;
}

export class TableAction {
  id?: string;
  title?: () => string;
  tooltip?: () => string;
  icon?: string;
  type?: string = TableConstant.ActionType.Inline;
  customClass?: string;
  executeAsync?: (item?: any, element?: ElementRef, provider?: any, index?: number, loadedCallback?: Function) => any;
  disabled?: boolean;
  hide?: (item?: any) => boolean;
  lazyload?: boolean;
  constructor(init?: Partial<TableAction>) {
    Object.assign(this, init);
  }
}

export interface TableRequest {
  searchText?: string;
  pageSize?: number;
  pageIndex?: number;
  sorters?: TableSorter[];
  data?: any;
}

export class TableText {
  placeholderSearch?: string = TableConstant.DisplayText.PlaceholderSearch;
  btnSearch?: string = TableConstant.DisplayText.BtnSearch;
  btnReset?: string = TableConstant.DisplayText.BtnReset;
  action?: string = TableConstant.DisplayText.Action;
  selectPageSize?: string = TableConstant.DisplayText.SelectPageSize;
  deleteTitle?: string = TableConstant.DisplayText.DeleteTitle;
  btnAcceptTitle?: string = TableConstant.DisplayText.BtnAcceptTitle;
  btnCancelTitle?: string = TableConstant.DisplayText.BtnCancelTitle
  filterTitle?: string = TableConstant.DisplayText.FilterTitle;
  applyFilter?: string = TableConstant.DisplayText.ApplyFilter;
  detailTitle?: string = TableConstant.DisplayText.DetailTitle;
  pageTitle?: string = TableConstant.DisplayText.PageTitle;
  advancedSearchTitle?: string = TableConstant.DisplayText.AdvancedSearchTitle;
  advancedBtnTitle?: string = TableConstant.DisplayText.AdvancedBtnTitle;
  advancedBtnCancelTitle?: string = TableConstant.DisplayText.AdvancedBtnCancelTitle;
  allTitle?: string = TableConstant.DisplayText.AllTitle;
}

export class TableMessage {
  notFoundMessage?: string = TableConstant.Message.NotFoundMessage;
  foundMessage?: string = TableConstant.Message.FoundMessage;
  invalidFormatMessage?: string = TableConstant.Message.InvalidFormatMessage;
  selectedItemsMessage?: string = TableConstant.Message.SelectedItemsMessage;
  confirmSelectAllRecordsMessage?: string = TableConstant.Message.ConfirmSelectAllRecordsMessage;
  confirmClearAllRecordsMessage?: string = TableConstant.Message.ConfirmClearAllRecordsMessage;
  deleteMessage?: string = TableConstant.Message.DeleteMessage;
  loadingMessage?: string = TableConstant.Message.LoadingMessage;
  refMessage?: string = TableConstant.Message.RefMessage;
}

export interface TableResponse<T> {
  totalRecords?: number;
  items?: T[];
}

export interface TableServiceProvider {
  searchAsync?: (request?: any) => Observable<any>;
  createAsync?: (item: any) => Observable<any>;
  updateAsync?: (item: any) => Observable<any>;
  deleteAsync?: (ids: string, all?: boolean) => Observable<any>;
  exportAsync?: (request?: any) => Observable<any>;
}

export class TableDatetimeFormat {
  format: string = 'MM/dd/yyyy';
  full: boolean = true;
  constructor(init: Partial<TableDatetimeFormat>) {
    Object.assign(this, init);
  }
}

export class EdittedField {
  item: any;
  field?: string;
  index?: number;
  constructor(init: Partial<EdittedField>) {
    Object.assign(this, init);
  }
}

export class TableOption {
  sort?: (a: any, b: any, orderBy: string) => number;
  multiple?: boolean = true;
  datetimeFormat?: TableDatetimeFormat = new TableDatetimeFormat({});
  paging?: boolean;
  selectedItems?: any[];
  serviceProvider?: TableServiceProvider;
  localData?: () => any[];
  request?: TableRequest;
  mainColumns: TableColumn[] = [];
  displayText?: TableText;
  message?: TableMessage;
  componentClass?: string;
  actions?: TableAction[] = [];
  topButtons?: TableAction[] = [];
  rowDetailTemplate?: Type<any>;
  expandFilterArea?: boolean;
  pageSizes?: number[];
  defaultPageSize?: number = 5;
  totalToolbarItem?: number = 5;
  maxPage?: number;
  key?: string;
  title?: string;
  maxLenghtext?: number;
  mode: TableMode = TableMode.full;
  hideSequenceColumn?: boolean = false;
  hideCheckboxColumn?: boolean = false;
  displayMode?: 'list' | 'full';
  defaultOrderBy?: string;
  defautOrderDirection?: string;
  inlineEdit?: boolean = false;
  searchFields?: string[];
  constructor(init: Partial<TableOption>) {
    Object.assign(this, init);
  }
}

export enum TableMode {
  compact = 'compact',
  full = 'full'
}


