export enum TableColumnType {
  Number = 'number',
  String = 'string',
  Date = 'date',
  DateTime = 'datetime',
  DateRange = 'daterange',
  DateTimeRange = 'datetimerange',
  Time = 'time',
  TimeRange = 'timerange',
  Boolean = 'boolean',
  Description = 'description',
  Currency = 'currency',
  Dropdown = 'dropdown',
  Custom = 'custom',
}