import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';
import { SettingService } from './setting.service';
import { Breadcrumb, MenuTab } from '../models/base.model';

@Injectable({ providedIn: 'root' })
export class ActionService {
    constructor(
        private _settingService: SettingService
    ) { }

    public executeAsync(callback: () => any, timeout: number = 50): void {
        setTimeout(() => {
            if (callback) callback();
        }, timeout);
    }

    public scrollTop(): void {
        window.scrollTo(0, 0);
    }

    public isMobile(): boolean {
        const userAgent = navigator.userAgent;
        return (/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini|Mobile|mobile|CriOS/i.test(userAgent));
    }

    public verify<T>(callback: Observable<T>): Observable<T> {
        var mock = this._settingService.useMock();
        if (mock) return of({} as T);
        return callback;
    }

    public changeItem(item: any, menuTabs: MenuTab[], callback?: (breadCrumbs: Breadcrumb[]) => any): Breadcrumb[] {
        var items: Breadcrumb[] = [];
        var parent = <Breadcrumb>this.retrieveParent(item.state, menuTabs);
        if (parent != null) {
            items.push(parent);
        }
        if (!items.find(s => s.label == item.name)) {
            items.push(new Breadcrumb({
                label: item.name,
                url: item.mainState ? `/${item.mainState}/${item.state}` : `${item.state}`
            }));
        }
        if (callback) callback(items);
        return items;
    }

    public retrieveParent(state: string, menuTabs: MenuTab[]): Breadcrumb {
        if (!menuTabs || menuTabs.length == 0) return;
        for (let i = 0; i < menuTabs.length; i++) {
            var items = menuTabs[i].items;
            if (!items || items.length == 0) continue;
            for (let j = 0; j < items.length; j++) {
                var children = items[j].children;
                if (!children || children.length == 0) return;
                for (let k = 0; k < children.length; k++) {
                    if (children[k].state == state) return new Breadcrumb({
                        label: items[j].label,
                        state: children[k].mainState,
                        url: children[k].mainState ? `/${children[k].mainState}/${children[0].state}` : `${children[0].state}`
                    });
                }
            }
        }
        return null;
    }

    public retrieveChild(state: string, menuTabs: MenuTab[]): Breadcrumb {
        if (!menuTabs || menuTabs.length == 0) return;
        for (let i = 0; i < menuTabs.length; i++) {
            var items = menuTabs[i].items;
            if (!items || items.length == 0) continue;
            for (let j = 0; j < items.length; j++) {
                var children = items[j].children;
                if (!children || children.length == 0) return;
                for (let k = 0; k < children.length; k++) {
                    if (children[k].mainState == state) {
                        return new Breadcrumb({
                            state: children[k].mainState,
                            label: children[k].name,
                            url: `/${children[k].mainState}/${children[0].state}`
                        });
                    }
                }
            }
        }
        return null;
    }

    public retrieveStateName(state: string, menuTabs: MenuTab[]): string {
        if (!menuTabs || menuTabs.length == 0) return;
        for (let i = 0; i < menuTabs.length; i++) {
            var items = menuTabs[i].items;
            if (!items || items.length == 0) continue;
            for (let j = 0; j < items.length; j++) {
                var children = items[j].children;
                if (!children || children.length == 0) return;
                for (let k = 0; k < children.length; k++) {
                    if (children[k].state == state) return children[k].name;
                }
            }
        }
        return null;
    }
}
