import { Component, Input, OnInit } from "@angular/core";
import { Breadcrumb, MenuTab } from '../shared/models/base.model';
import { ActionService } from '../shared/services/action.service';
import { Router } from '@angular/router';
import { AggregatorService } from '../shared/services/aggregator.service';
import { KeyConst } from '../shared/constants/key.const';

@Component({
    selector: 'default-navbar',
    templateUrl: './navbar.component.html'
})

export class DefaultNavBarComponent implements OnInit {
    @Input() public hideBreadcrumbsSection: boolean = false;
    @Input() public items: Breadcrumb[];
    @Input() public menuTabs: MenuTab[] = [];
    @Input() public url: string;
    @Input() public breadcrumb: Breadcrumb;

    constructor(
        private _actionService: ActionService,
        private _router: Router,
        protected aggregatorService: AggregatorService
    ) { }

    ngOnInit(): void {
        if (!this.items || this.items.length == 0) {
            if (this.menuTabs && this.menuTabs.length > 0) {
                var data = this._router.url;
                var states = data.split('/');
                if (states.length > 0) {
                    var state = states[states.length - 1];
                    if (!state && this.url) {
                        data = this.url;
                        states = data.split('/');
                        state = states[states.length - 1];
                    }
                    if (state) {
                        var name = this._actionService.retrieveStateName(state, this.menuTabs);
                        this._actionService.changeItem({ state: state, name: name }, this.menuTabs, (items) => {
                            this.items = items;
                            this.items.splice(0, 0, this.breadcrumb);
                        });
                    }
                }
            }
        }

        this.aggregatorService.subscribe(KeyConst.MenuChanged, (items: Breadcrumb[]) => {
            this.items = items;
        });
    }

    public retrieveCurrentItem(): Breadcrumb {
        if (!this.items || this.items.length == 0) return null;
        return this.items[this.items.length - 1];
    }
}
