import { Component, Input, Output, EventEmitter, OnDestroy, ContentChild, AfterViewInit, ViewChildren, ViewChild, ElementRef } from '@angular/core';
import { FormDirectorExtraItemDirective } from '../form';
import { ActionService } from '../services/action.service';

@Component({
  selector: 'katana-textbox',
  templateUrl: './textbox.component.html',
  styleUrls: ['./textbox.component.scss']
})

export class TextboxComponent implements OnDestroy, AfterViewInit {
  @Input() public title: string;
  @Input() public model: string | number;
  @Input() public type: 'text' | 'currency' | 'phone' | 'email' | 'number' | 'password' = 'text';
  @Input() public name: string;
  @Input() public placeholder: string = 'in thng tin';
  @Input() public readonly: boolean = false;
  @Input() public disabled: boolean = false;
  @Input() public maxCharacters: number = 100;
  @Input() public minNumber: number;
  @Input() public maxNumber: number;
  @Input() public validationName: string;
  @Input() public validationScope: string;
  @Input() public direction: 'horizontal' | 'vertical' = 'vertical';
  @Input() public alignVertical: boolean = true;
  @Input() public controlName: string;
  @Input() public description: string;
  @Input() public emitNullOnDestroy: boolean = false;
  @Input() public suffix: string;
  @Input() public item: any;
  @Input() public focus: boolean;
  @Output() public modelChange: EventEmitter<string | number> = new EventEmitter();
  @ContentChild(FormDirectorExtraItemDirective, { static: true }) extraLabelItem: FormDirectorExtraItemDirective;
  @ViewChild('itemRef', { static: true }) public itemRef: ElementRef;

  constructor(
    private _actionService: ActionService
  ) { }

  ngAfterViewInit(): void {
    if (this.focus) {
      this._actionService.executeAsync(() => {
        var elements = document.getElementsByTagName('input');
        if (elements) {
          for (let i = 0; i < elements.length; i++) {
            if (elements[i].id == this.item.id) {
              elements[i].focus();
              break;
            }
          }
        }
      });
    }
  }

  ngOnDestroy() {
    if (this.emitNullOnDestroy === true) {
      this.modelChange.emit(null);
    }
  }

  public submitNumeric(newValue: number) {
    if (this.minNumber && newValue < this.minNumber) {
      this.modelChange.emit(this.minNumber);
      event.preventDefault();
      return;
    }
    if (this.maxNumber && newValue > this.maxNumber) {
      this.modelChange.emit(this.maxNumber);
      event.preventDefault();
      return;
    }
    this.modelChange.emit(newValue);
  }
}
