import { Injectable } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';
import { filter } from 'rxjs/operators';
import { Subscription } from 'rxjs';
import { ButtonDefinition } from '../shared/models/base.model';
import { ToolbarAction } from '../shared/enums/base.enum';
import { AggregatorService } from '../shared/services/aggregator.service';
import { KeyConst } from '../shared/constants';

@Injectable({ providedIn: 'root' })
export class DefaultLayoutService {
    public navType: string;
    public themeLayout: string;
    public layoutType: string;
    public verticalPlacement: string;
    public verticalLayout: string;
    public deviceType: string;
    public imgSrc: string;
    public imgTitle: string;
    public verticalNavType: string;
    public verticalEffect: string;
    public vNavigationView: string;
    public pcodedHeaderPosition: string;
    public pcodedSidebarPosition: string;
    public headerTheme: string;
    public logoTheme: string;
    public innerHeight: string;
    public windowWidth: number;
    public toggleOn: boolean;
    public headerFixedMargin: string;
    public navBarTheme: string;
    public activeItemTheme: string;
    public isCollapsedMobile: string;
    public chatToggle: string;
    public chatToggleInverse: string;
    public chatInnerToggle: string;
    public chatInnerToggleInverse: string;
    public menuTitleTheme: string;
    public itemBorder: boolean;
    public itemBorderStyle: string;
    public subItemBorder: boolean;
    public subItemIcon: string;
    public dropDownIcon: string;
    public isSidebarChecked: boolean;
    public isHeaderChecked: boolean;
    private subscriptions: Subscription = new Subscription();
    private toolbarItems: ButtonDefinition[] = [];

    constructor(
        private router: Router,
        private _aggregatorService: AggregatorService
    ) {
        this.navType = 'st5';
        this.themeLayout = 'vertical';
        this.vNavigationView = 'view1';
        this.verticalPlacement = 'left';
        this.verticalLayout = 'wide';
        this.deviceType = 'desktop';
        this.verticalNavType = 'offcanvas';
        this.verticalEffect = 'shrink';
        this.pcodedHeaderPosition = 'fixed';
        this.pcodedSidebarPosition = 'fixed';
        this.headerTheme = 'theme1';
        this.logoTheme = 'theme1';
        this.toggleOn = true;
        this.headerFixedMargin = '50px';
        this.navBarTheme = 'themelight1';
        this.activeItemTheme = 'theme4';
        this.isCollapsedMobile = 'no-block';
        this.chatToggle = 'out';
        this.chatToggleInverse = 'in';
        this.chatInnerToggle = 'off';
        this.chatInnerToggleInverse = 'on';
        this.menuTitleTheme = 'theme5';
        this.itemBorder = true;
        this.itemBorderStyle = 'none';
        this.subItemBorder = true;
        this.subItemIcon = 'style6';
        this.dropDownIcon = 'style1';
        this.isSidebarChecked = true;
        this.isHeaderChecked = true;
        const scrollHeight = window.screen.height - 150;
        this.innerHeight = scrollHeight + 'px';
        this.windowWidth = window.innerWidth;
        this.setMenuAttributes(this.windowWidth);

        const navigationSubscription = this.router.events.pipe(filter(event => event instanceof NavigationEnd)).subscribe(event => {
            this.toolbarItems = [];
        });
        this.subscriptions.add(navigationSubscription);
    }

    ngOnDestroy() {
        this.subscriptions.unsubscribe();
    }

    public onResize(event) {
        this.innerHeight = event.target.innerHeight + 'px';
        this.windowWidth = event.target.innerWidth;
        let reSizeFlag = true;
        if (this.deviceType === 'tablet' && this.windowWidth >= 768 && this.windowWidth <= 1024) {
            reSizeFlag = false;
        } else if (this.deviceType === 'mobile' && this.windowWidth < 768) {
            reSizeFlag = false;
        }
        if (reSizeFlag) {
            this.setMenuAttributes(this.windowWidth);
        }
    }

    public setMenuAttributes(windowWidth) {
        if (windowWidth >= 768 && windowWidth <= 1024) {
            this.deviceType = 'tablet';
            this.verticalNavType = 'offcanvas';
            this.verticalEffect = 'push';
        } else if (windowWidth < 768) {
            this.deviceType = 'mobile';
            this.verticalNavType = 'offcanvas';
            this.verticalEffect = 'overlay';
        } else {
            this.deviceType = 'desktop';
            this.verticalNavType = 'offcanvas';
            this.verticalEffect = 'shrink';
        }
    }

    public toggleOpened() {
        if (this.windowWidth < 768) {
            this.toggleOn = this.verticalNavType === 'offcanvas' ? true : this.toggleOn;
        }
        this.verticalNavType = this.verticalNavType === 'expanded' ? 'offcanvas' : 'expanded';
    }

    public onClickedOutside(e: Event) {
        if (this.windowWidth < 768 && this.toggleOn && this.verticalNavType !== 'offcanvas') {
            this.toggleOn = true;
            this.verticalNavType = 'offcanvas';
        }
    }

    public onMobileMenu() {
        this.isCollapsedMobile = this.isCollapsedMobile === 'yes-block' ? 'no-block' : 'yes-block';
    }

    public toggleChat() {
        this.chatToggle = this.chatToggle === 'out' ? 'in' : 'out';
        this.chatToggleInverse = this.chatToggleInverse === 'out' ? 'in' : 'out';
        this.chatInnerToggle = 'off';
        this.chatInnerToggleInverse = 'off';
    }

    public toggleChatInner() {
        this.chatInnerToggle = this.chatInnerToggle === 'off' ? 'on' : 'off';
        this.chatInnerToggleInverse = this.chatInnerToggleInverse === 'off' ? 'on' : 'off';
    }

    public setSidebarPosition() {
        this.isSidebarChecked = !this.isSidebarChecked;
        this.pcodedSidebarPosition = this.isSidebarChecked === true ? 'fixed' : 'absolute';
    }

    public setHeaderPosition() {
        this.isHeaderChecked = !this.isHeaderChecked;
        this.pcodedHeaderPosition = this.isHeaderChecked === true ? 'fixed' : 'relative';
        this.headerFixedMargin = this.isHeaderChecked === true ? '50px' : '';
    }

    public setBackgroundPattern(pattern) {
        document.querySelector('body').setAttribute('themebg-pattern', pattern);
    }

    public setLayoutType(type: string) {
        this.layoutType = type;
        if (type === 'dark') {
            this.headerTheme = 'theme6';
            this.navBarTheme = 'theme1';
            this.logoTheme = 'theme6';
            document.querySelector('body').classList.add('dark');
        } else {
            this.headerTheme = 'theme1';
            this.navBarTheme = 'themelight1';
            this.logoTheme = 'theme1';
            document.querySelector('body').classList.remove('dark');
        }
    }

    public setNavBarTheme(theme: string) {
        if (theme === 'themelight1') {
            this.navBarTheme = 'themelight1';
        } else {
            this.navBarTheme = 'theme1';
        }
    }

    public initToolbarItems(actions: string[]) {
        this.toolbarItems = actions.map(action => {
            switch (action) {
                case ToolbarAction.Save:
                    return new ButtonDefinition({ action: action, title: 'Lu', style: 'primary', lazyload: true });
                case ToolbarAction.SaveAll:
                    return new ButtonDefinition({ action: action, title: 'Lu tt c', lazyload: true });
                case ToolbarAction.Cancel:
                    return new ButtonDefinition({ action: action, title: 'Hy lu' });
                case ToolbarAction.Back:
                    return new ButtonDefinition({ action: action, title: 'Quay li' });
                case ToolbarAction.EditColumnOptions:
                    return new ButtonDefinition({ action: action, title: 'Qun l ct' });
                case ToolbarAction.SaveAndNew:
                    return new ButtonDefinition({ action: action, title: 'Lu v to mi', lazyload: true });
                case ToolbarAction.Reset:
                    return new ButtonDefinition({ action: action, title: 'Lm mi' });
            }
        });
    }

    public getToolbarItems() {
        return this.toolbarItems;
    }

    public hasToolbarItems(): boolean {
        if (!this.toolbarItems) return false;
        return this.toolbarItems.length > 0;
    }

    public showImage(image: any) {
        this._aggregatorService.publish(KeyConst.ViewImage, image);
    }
}
