import { Component, ViewChild, TemplateRef, Input } from '@angular/core';

@Component({
  selector: 'katana-radio-item',
  templateUrl: './radio-item.component.html',
  styleUrls: ['./radio-item.component.scss']
})

export class RadioItemComponent {
  @ViewChild(TemplateRef, { static: true }) public template: TemplateRef<any>;
  @Input() public value: any;
  @Input() public label: string;
  @Input() public disabled: boolean = false;
  @Input() public checked: boolean = false;
  public groupIndex: number;
  private selectedCallBack: Function;

  public changeItem($event: Event) {
    $event.stopPropagation();
    if (this.selectedCallBack) {
      this.selectedCallBack(this);
    }
  }

  public select($event) {
    $event.stopPropagation();
  }

  public registerSelectedCallback(callback: Function) {
    this.selectedCallBack = callback;
  }
}
