import { MockData, Audit } from '../models';

export class FileViewModel extends Audit {
    id?: string;
    name?: string;
    src?: string;
    type?: string;
    size?: number;
    constructor(init?: Partial<FileViewModel>) {
        super();
        Object.assign(this, init);
    }
}

export class FileRequest extends MockData<FileResponse> {
    items: FileViewModel[];
    others?: FileViewModel[];
    createdBy: string;
    constructor(init?: Partial<FileRequest>) {
        super();
        Object.assign(this, init);
    }
}

export class FileResponse {
    code?: string;
    status?: boolean;
    message?: string;
    totalRecords?: number;
    items: FileViewModel[] = [];
    constructor(init?: Partial<FileResponse>) {
        Object.assign(this, init);
    }
}