import { Component, OnInit, Input, Output, EventEmitter, OnDestroy } from '@angular/core';
import { take, finalize, tap } from 'rxjs/operators';
import { HttpEventType } from '@angular/common/http';
import { Subscription } from 'rxjs';
import { FileViewModel, FileRequest, FileResponse } from './file.model';
import { CropperComponent } from '../cropper/cropper.component';
import { FileService } from './file.service';
import { NotificationViewModel } from '../modals/modal.model';
import { AuthenticationService } from '../../auth/auth.service';
import { ModalService } from '../modals/modal.service';

@Component({
  selector: 'katana-file-uploader',
  templateUrl: './file.component.html',
  styleUrls: ['./file.component.scss']
})

export class UploaderComponent implements OnInit, OnDestroy {
  @Input() public icon: string = 'fa fa-twitter';
  @Input() public title: string = 'Chnh sa nh';
  @Input() public multiple: boolean = true;
  @Input() public cropImage: boolean;
  @Input() public validationName: string;
  @Input() public maxSize: number = 10 * 1024 * 1024;
  @Input() public fileType: 'doc' | 'image' | 'audio' | 'video' | 'default' = 'default';
  @Input() public cutRatio: number;
  @Input() public resizeToWidth: number;
  @Input() public maintainAspectRatio: boolean;
  @Output('checkedLoading') changeLoading = new EventEmitter<boolean>();
  @Output('checkedProgress') changeProgress = new EventEmitter<number>();
  @Output() public fileUploaded: EventEmitter<FileViewModel> = new EventEmitter();
  public loading: boolean;
  public progress: number = 0;
  private currentTypes: any[];
  private subscriptions: Subscription = new Subscription();

  constructor(
    protected modalService: ModalService,
    protected fileService: FileService,
    protected authenticationService: AuthenticationService
  ) { }

  ngOnInit(): void {
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  public onFileUploaded(files: File[], event: any) {
    if (!files || files.length == 0) return;
    this.execute(files, event);
  }

  private execute(files: File[], event: any): void {
    if (files.length == 0) return;
    var file = files[0];
    const validationResult = this.initValidation(file);
    if (!validationResult.isValid) {
      validationResult.errors.forEach(x => {
        switch (x) {
          case 'Size':
            this.modalService.showNotificationDialog(new NotificationViewModel({
              message: `Kch thc file phi nh hn ${this.maxSize / (1024 * 1024)} mb`
            }));
            break;
          case 'Type':
            this.modalService.showNotificationDialog(new NotificationViewModel({
              message: `Loi file khng ph hp`
            }));
            break;
        }
      });
      return;
    }
    if (this.cropImage) {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onloadend = (loadEvent: any) => {
        const image = new FileViewModel({
          createdDate: new Date(),
          lastModifiedDate: new Date(),
          createdBy: this.authenticationService.currentUser ? this.authenticationService.currentUser.id : null,
          lastModifiedBy: this.authenticationService.currentUser ? this.authenticationService.currentUser.id : null
        });
        image.src = loadEvent.target.result;
        this.modalService.showTemplateDialog({
          title: this.title,
          customSize: 'modal-lg',
          mode: 'Custom',
          autoClose: true,
          icon: this.icon,
          data: {
            imageElement: image,
            eventFile: event,
            cutRatio: this.cutRatio,
            maintainAspectRatio: this.maintainAspectRatio,
            resizeToWidth: this.resizeToWidth
          },
          template: CropperComponent,
          acceptCallback: (base64String) => {
            const base64FileParts = base64String.split(',');
            if (base64FileParts.length < 2) {
              return;
            }
            const base64FileData = base64FileParts[1];
            this.loading = true;
            this.changeLoading.emit(this.loading);
            this.fileService.uploadFiles(new FileRequest({
              createdBy: this.authenticationService.currentUser ? this.authenticationService.currentUser.id : null,
              items: [new FileViewModel({
                src: base64FileData,
                name: file.name,
                size: file.size,
                type: file.type,
                createdDate: new Date(),
                lastModifiedDate: new Date(),
                createdBy: this.authenticationService.currentUser ? this.authenticationService.currentUser.id : null,
                lastModifiedBy: this.authenticationService.currentUser ? this.authenticationService.currentUser.id : null
              })],
              mockData: new FileResponse({
                status: true,
                items: [
                  new FileViewModel({
                    src: base64String,
                    size: file.size,
                    name: file.name,
                    createdDate: new Date(),
                    lastModifiedDate: new Date(),
                    createdBy: this.authenticationService.currentUser ? this.authenticationService.currentUser.id : null,
                    lastModifiedBy: this.authenticationService.currentUser ? this.authenticationService.currentUser.id : null
                  })
                ]
              })
            })).pipe(take(1), finalize(() => {
              this.loading = false;
              this.changeLoading.emit(this.loading);
            })).subscribe(response => {
              if (response.items.length > 0) {
                const uploadedFilePath = response.items[0].src;
                this.fileUploaded.emit(new FileViewModel({
                  src: uploadedFilePath,
                  name: file.name,
                  size: file.size,
                  type: file.type,
                  createdDate: new Date(),
                  lastModifiedDate: new Date(),
                  createdBy: this.authenticationService.currentUser ? this.authenticationService.currentUser.id : null,
                  lastModifiedBy: this.authenticationService.currentUser ? this.authenticationService.currentUser.id : null
                }));
              }
            });
          }
        })
      };
    } else {
      this.loading = true;
      this.changeLoading.emit(this.loading);
      const uploadFileSubscription = this.fileService.uploadProgress(file).pipe(
        tap((event => {
          if (event.type === HttpEventType.UploadProgress) {
            this.progress = Math.round((100 * event.loaded) / event.total);
            this.changeProgress.emit(this.progress);
          } else if (event.type === HttpEventType.Response) {
            const response = event.body;
            this.progress = 0;
            this.loading = false;
            this.changeProgress.emit(this.progress);
            this.changeLoading.emit(this.loading);
            this.fileUploaded.emit(new FileViewModel({
              src: response.items && response.items.length > 0 ? response.items[0].src : null,
              name: file.name
            }));
          }
        }),
          (err) => {
            console.log(err);
          }
        )).subscribe();
      this.subscriptions.add(uploadFileSubscription);
    }
  }

  private initValidation(file: File): { isValid: Boolean; errors: any[] } {
    let isValid: boolean = true;
    let errors = [];
    if (this.fileType) {
      this.initExtentionFile(this.fileType);
      const types = file.name.split('.');
      const extension = types[types.length - 1];
      isValid = this.currentTypes.indexOf(extension) >= 0;
      if (!isValid) {
        errors.push('Type');
        return { isValid: isValid, errors: errors };
      }
    }

    if (this.maxSize && this.maxSize !== 0) {
      isValid = +file.size < +this.maxSize;
      if (!isValid) {
        errors.push('Size');
        return { isValid: isValid, errors: errors };
      };
    }
    return { isValid: isValid, errors: errors };
  }

  private initExtentionFile(fileType: string) {
    switch (fileType) {
      case 'doc':
        this.currentTypes = ['csv', 'doc', 'docx', 'log', 'msg', 'rtf', 'txt', 'wpf', 'pdf', 'csv', 'ppt', 'pps', 'vcf', 'xlr', 'xls', 'xlsx'];
        break;
      case 'image':
        this.currentTypes = ['bmp', 'dds', 'gif', 'heic', 'jpg', 'png', 'psd', 'thm', 'tif', 'jpe', 'jpeg'];
        break;
      case 'audio':
        this.currentTypes = ['aif', 'iff', 'm3u', 'm4a', 'mid', 'mp3', 'mpa', 'wav', 'wma'];
        break;
      case 'video':
        this.currentTypes = ['3g2', '3gp', 'avi', 'flv', 'm4v', 'mov', 'mp4', 'mpg', 'wmv'];
        break;
      case 'default':
        this.currentTypes = ['csv', 'doc', 'docx', 'log', 'msg', 'rtf', 'txt', 'wpf', 'pdf', 'csv', 'ppt', 'pps', 'vcf', 'xlr', 'xls', 'xlsx',
          'bmp', 'dds', 'gif', 'heic', 'jpg', 'png', 'psd', 'thm', 'tif', 'jpe', 'jpeg',
          'aif', 'iff', 'm3u', 'm4a', 'mid', 'mp3', 'mpa', 'wav', 'wma',
          '3g2', '3gp', 'avi', 'flv', 'm4v', 'mov', 'mp4', 'mpg', 'wmv'
        ];
        break;
      default: return;
    }
  }
}
