import { Component, ViewEncapsulation } from '@angular/core';
import { DomSanitizer, SafeHtml } from '@angular/platform-browser';
import { BsModalRef } from 'ngx-bootstrap';
import { NotificationViewModel } from '../../modal.model';

@Component({
    selector: 'katana-notification',
    templateUrl: './notification.component.html',
    styleUrls: ['./notification.component.scss'],
    encapsulation: ViewEncapsulation.None
})

export class NotificationComponent {
    public data: NotificationViewModel;
    constructor(
        private _sanitizationService: DomSanitizer,
        public _modalRef: BsModalRef) {
    }

    public bypassSecurityTrustHtml(html: string): SafeHtml {
        return this._sanitizationService.bypassSecurityTrustHtml(html);
    }

    public cancel() {
        if (this.data.callback) {
            this.data.callback(() => {
                this._modalRef.hide();
            });
        } else {
            this._modalRef.hide();
        }
        if (this.data.autoClose != false) {
            this._modalRef.hide();
        }
    }
}
