import { Component, OnInit, Input, ViewChild, ElementRef, ContentChild, Renderer2, RendererFactory2, AfterViewInit, OnDestroy } from '@angular/core';
import { trigger, state, style, transition, animate } from '@angular/animations';
import { TableOption, TableColumn, TableAction, TableMode, TableConstant, TableResponse, TableText, TableMessage, TableColumnType, EdittedField } from './table.model';
import { TableRowDetailDirective } from './table-row-detail.directive';
import { Observable, of, BehaviorSubject, Subscription } from 'rxjs';
import { distinctUntilChanged } from 'rxjs/operators';
import { DataService } from '../services/data.service';
import { delay } from "rxjs/operators";

@Component({
  selector: 'katana-table',
  templateUrl: './table.component.html',
  styleUrls: ['./table.component.scss'],
  animations: [
    trigger('cAnimation', [
      state('void', style({})),
      state('*', style({})),
      transition(':enter', [
        style({ transform: 'translateY(-30px)', opacity: 0 }),
        animate('300ms ease-in-out', style({ transform: 'translateY(0)', opacity: 1 }))
      ]),
      transition(':leave', [
        style({ transform: 'translateY(0)', opacity: 1 }),
        animate('300ms ease-in-out', style({ transform: 'translateY(-30px)', opacity: 0 }))
      ])
    ]),
    trigger('cAnimationFadeRight', [
      state('void', style({})),
      state('*', style({})),
      transition(':enter', [
        style({ transform: 'translateX(30px)', opacity: 0 }),
        animate('300ms ease-in-out', style({ transform: 'translateX(0)', opacity: 1 }))
      ]),
      transition(':leave', [
        style({ transform: 'translateX(0)', opacity: 1 }),
        animate('300ms ease-in-out', style({ transform: 'translateX(30px)', opacity: 0 }))
      ])
    ])
  ]
})

export class TableComponent implements OnInit, AfterViewInit, OnDestroy {
  @Input() validationName: string;
  @Input() validationScope: string;
  @Input() public option: TableOption;
  @ViewChild('searchRef', { static: true }) public searchRef: ElementRef;
  @ViewChild('gotoRef', { static: true }) public gotoRef: ElementRef;
  @ViewChild('tableRef', { static: true }) public tableRef: ElementRef;
  @ContentChild(TableRowDetailDirective, { static: true }) public rowDetailTemplate: TableRowDetailDirective;
  public items: any[] = [];
  public totalRecords: number = 0;
  public loading: boolean;
  public selectedItems: any[] = [];
  public pageSize: number;
  public orderBy: string;
  public direction: string;
  public totalPages: number[] = [];
  public currentPage: number = 0;
  public filter: any = {};
  public maxPage: number = 5;
  public selectedAll: boolean = false;
  public filterColumns: TableColumn[] = [];
  public toolbarActions: TableAction[] = [];
  public inlineActions: TableAction[] = [];
  public filterSectionToggle: boolean = false;
  public textSearched: string = ``;
  public showReset: boolean = false;
  private defaultPageSize: number = 5;
  private localData?: any[];
  private _render: Renderer2;
  public hasFilterSection: boolean;
  public hasToolbarSection: boolean;
  public hasFooterSection: boolean;
  public hasDetailTemplate: boolean;
  public hasPageSizeChooser: boolean;
  public changePage$: BehaviorSubject<number> = new BehaviorSubject(0);
  private request: any = {};
  private previousRequest: any = {};
  private recursiveCounter: number = 0;
  private subscriptions: Subscription = new Subscription();
  protected edittedFields: EdittedField[] = [];

  constructor(
    private thisElement: ElementRef,
    protected rendererFactory: RendererFactory2,
    protected dataService: DataService
  ) {
    this._render = rendererFactory.createRenderer(null, null);
  }

  ngOnInit() {
    this.init();
    const changePageSubscription = this.changePage$.pipe(distinctUntilChanged()).subscribe(pageIndex => {
      if (pageIndex < 0 || pageIndex >= this.totalPages.length) return;
      this.currentPage = pageIndex;
      if (!this.option.request) {
        this.option.request = {};
      }
      this.option.request.pageIndex = this.currentPage;
      this.searchAsync(null, null, true).subscribe();
    });
    this.subscriptions.add(changePageSubscription);
  }

  ngAfterViewInit(): void {
    this.registerEvents();
  }

  ngOnDestroy() {
    this.subscriptions.unsubscribe();
  }

  public init(): void {
    if (this.option.selectedItems && this.option.selectedItems.length > 0) {
      if (!this.selectedItems) this.selectedItems = [];
      this.option.selectedItems.forEach((item) => {
        this.selectedItems.push(item);
      });
    }

    if (!this.option.mode) this.option.mode = TableMode.full;
    if (!this.option.actions) this.option.actions = [];
    if (!this.option.key) this.option.key = TableConstant.Key;
    if (!this.option.totalToolbarItem) this.option.totalToolbarItem = 5;
    if (this.option.maxPage) this.maxPage = this.option.maxPage;
    if (!this.option.defaultOrderBy) this.option.defaultOrderBy = 'CreatedDate';
    if (!this.option.defautOrderDirection) this.option.defautOrderDirection = TableConstant.Direction.DESC;
    if (!this.option.componentClass) {
      this.option.componentClass = TableConstant.ComponentClass;
      this.thisElement.nativeElement.classList.add(this.option.componentClass);
    }

    if (this.option.maxLenghtext === undefined || this.option.maxLenghtext === null) {
      this.option.maxLenghtext = 150;
    }

    this.initTableTableTexts();
    this.initTableTableMessages();
    this.initMainColumns();
    if (this.option.actions) {
      this.option.actions.forEach((action) => {
        if (!action.type) action.type = TableConstant.ActionType.Inline;
      });
    }
    this.toolbarActions = this.option.actions.filter(x => [TableConstant.ActionType.Both, TableConstant.ActionType.Toolbar].indexOf(x.type) >= 0);
    this.inlineActions = this.option.actions.filter(x => [TableConstant.ActionType.Both, TableConstant.ActionType.Inline].indexOf(x.type) >= 0);

    const inFullMode = this.option.mode === TableMode.full;
    this.filterColumns = this.option.mainColumns.filter(column => column.allowFilter)
      .sort((a: TableColumn, b: TableColumn) => a.order > b.order ? 1 : a.order === b.order ? 0 : -1);
    this.hasFilterSection = inFullMode;

    if (this.option.paging === undefined) {
      this.option.paging = inFullMode;
    }

    if (inFullMode) {
      if (!this.option.pageSizes) this.option.pageSizes = TableConstant.PageSizes;
      if (!this.option.defaultPageSize) this.option.defaultPageSize = TableConstant.PageSizes[0];
      if (this.option.defaultPageSize) this.defaultPageSize = this.option.defaultPageSize;
    }

    const hasToolbarActions = this.option.actions && this.option.actions.filter(x => [TableConstant.ActionType.Toolbar, TableConstant.ActionType.Both].indexOf(x.type) >= 0).length > 0;
    const hasTopButtons = this.option.topButtons && this.option.topButtons.length > 0;

    this.hasToolbarSection = inFullMode || hasToolbarActions || hasTopButtons;
    this.hasFooterSection = inFullMode || this.option.paging;
    this.hasPageSizeChooser = this.option.paging;
    if (this.option.hideCheckboxColumn === undefined) {
      this.option.hideCheckboxColumn = !hasToolbarActions;
    }

    if (!this.option.request) {
      this.option.request = {
        pageIndex: 0,
        pageSize: this.defaultPageSize
      };
      if (this.option.defaultPageSize) {
        this.option.request.pageSize = this.option.defaultPageSize;
        this.pageSize = this.option.request.pageSize;
      }
      this.searchAsync().subscribe();
    } else {
      if (!this.option.request.pageSize) {
        this.pageSize = this.option.request.pageSize;
      } else {
        this.pageSize = this.defaultPageSize;
      }
      this.searchAsync().subscribe();
    }
  }

  public callback(): Observable<any> {
    return of(this.selectedItems);
  }

  public copy(item: any, refresh: boolean = true, execute?: (item: any) => void, callback?: (copyItem: any) => void): void {
    if (!this.items) this.items = [];
    var copyItem = this.dataService.cloneItem(item);
    if (copyItem.id) copyItem.id = this.dataService.newGuid();
    if (execute) {
      execute(copyItem);
    }
    if (this.option.localData) {
      this.localData.push(copyItem);
    }
    this.items.push(copyItem);
    if (callback) callback(copyItem);
    if (refresh == true) {
      this.searchAsync(true).subscribe();
    }
  }

  public acceptInlineEdit(field: string, index: number): void {
    this.closeInlineEdit(field, index);
  }

  public cancelInlineEdit(item: any, field: string, index: number): void {
    var currentItem = this.retrieveInlineEdit(field, index);
    if (!currentItem) return;
    item[field] = currentItem.item[field];
    this.closeInlineEdit(field, index);
  }

  public closeInlineEdit(field: string, index: number): void {
    var itemIndex = this.edittedFields.findIndex(s => s.field == field && s.index == index);
    if (itemIndex > -1) this.edittedFields.splice(itemIndex, 1);
  }

  public editInline(item: any, field: string, index: number): void {
    if (!item) return;
    var currentItem = this.retrieveInlineEdit(field, index);
    if (!currentItem) {
      this.edittedFields.push({
        item: this.dataService.cloneItem(item),
        index: index,
        field: field
      });
    } else {
      currentItem.item = this.dataService.cloneItem(item);
    }
  }

  public hasInlineEdit(item: any, field: string, index: number): boolean {
    if (!this.option || this.option.inlineEdit != true) return false;
    if (!item || !this.edittedFields || this.edittedFields.length == 0) return false;
    return (<EdittedField[]>this.edittedFields).findIndex(s => s.field == field && s.index == index) > -1;
  }

  public retrieveInlineEdit(field: string, index: number): EdittedField {
    if (!this.edittedFields || this.edittedFields.length == 0) return null;
    return (<EdittedField[]>this.edittedFields).find(s => s.field == field && s.index == index);
  }

  public resetFilters(): void {
    this.filter = {};
    this.selectedItems = [];
    this.selectedAll = false;
    this.filterSectionToggle = false;
    this.totalPages = [];
    this.option.request.pageIndex = 0;
    this.currentPage = 0;
    if (!this.option.request.pageSize) {
      this.pageSize = this.option.request.pageSize;
    } else {
      this.pageSize = this.defaultPageSize;
    }
    this.option.request.pageSize = this.pageSize;
    this.searchAsync().subscribe();
    this.showReset = false;
    this.request = {};
  }

  public getToolbarActions(standard: boolean): TableAction[] {
    let actions: TableAction[] = [];
    if (this.toolbarActions) {
      this.toolbarActions.forEach((action, index) => {
        if (!standard) {
          if (index >= this.option.totalToolbarItem) {
            actions.push(action);
          }
        } else {
          if (index < this.option.totalToolbarItem) {
            actions.push(action);
          }
        }
      });
    }
    return actions;
  }

  public changePageSize(): void {
    this.option.request.pageSize = this.pageSize;
    this.option.request.pageIndex = 0;
    this.searchAsync().subscribe();
  }

  public handkeKeypress($event): void {
    if ($event && $event.which == 13) {
      this.searchAsync().subscribe();
    }
  }

  public goto($event: any, blur?: boolean): void {
    if ($event.which == 13 || blur == true) {
      this.currentPage = $event.target.value - 1;
      if (this.currentPage < 0) this.currentPage = 0;
      if (this.currentPage >= this.totalPages.length) {
        this.currentPage = this.totalPages.length - 1;
      }
      $event.target.value = this.currentPage + 1;
      this.changePage$.next(this.currentPage);
    } else {
      if ($event.which < 48 || $event.which > 57) $event.preventDefault();
    }
  }

  public toggleFilterSection() {
    this.filterSectionToggle = !this.filterSectionToggle;
  }

  public showSorter(column: TableColumn, direction: string): boolean {
    return column.direction === direction;
  }

  public toggleRowDetail(item: any) {
    item.toggle = !item.toggle;
  }

  public sortAsync(column: TableColumn): void {
    if (!column.valueRef) return;
    this.resetSortAsync(column);
    if (column && column.allowSort != false) {
      if (!column.direction) {
        column.direction = TableConstant.Direction.ASC;
      } else {
        column.direction = column.direction == TableConstant.Direction.ASC ? TableConstant.Direction.DESC : TableConstant.Direction.ASC;
      }
    }
    this.orderBy = this.dataService.toPascalCase(column.valueRef());
    this.direction = column.direction;
    this.searchAsync(null, null, false).subscribe();
  }

  public selectAll(selected: boolean): void {
    this.selectedItems = selected ? [...this.items] : [];
  }

  public clearAll(): void {
    this.selectedItems = [];
    this.selectedAll = false;
  }

  public selectItem(item: any): void {
    const selectedIds = this.selectedItems.map(x => x.id);
    const existingItemIndex = selectedIds.indexOf(item.id);
    if (existingItemIndex >= 0) {
      this.selectedItems.splice(existingItemIndex, 1);
    } else {
      this.selectedItems.push(item);
    }
    if (!this.option.multiple) {
      const currentItem = this.selectedItems.find(s => s.id == item.id);
      if (currentItem) {
        this.selectedItems = [currentItem];
      }
    }
  }

  public isRowSelected(item: any) {
    const selectedIds = this.selectedItems.map(x => x.id);
    return selectedIds.indexOf(item.id) >= 0;
  }

  public hasAnyAction(item?: any): boolean {
    const aliveActions = this.inlineActions.filter(x => !x.hide || !x.hide(item));
    return aliveActions.length > 0;
  }

  public executeActionAsync(action: TableAction, item?: any, $event?: any, index?: number, loadedCallback?: Function): void {
    if (action) {
      const target = $event ? $event.target : null;
      action.executeAsync(item, target, this, index, loadedCallback);
    }
  }

  public isActive(item: any): boolean {
    if (this.selectedItems.length === 0) return false;

    const currentItem = this.selectedItems.find(s => s.id === item.id);
    return currentItem && currentItem.checkedRow;
  }

  public getCurrentIndex(index: number): number {
    if (this.option.request) {
      return this.option.request.pageIndex * this.option.request.pageSize + index + 1;
    }
    return -1;
  }

  public getDropdownDisplayText(column: TableColumn, item: any) {
    const values = item[column.valueRef()];
    if (!values) return '';

    if (values instanceof Array) {
      const result = <string[]>values.filter(x => x).map(x => x[column.dropdownConfiguration.bindLabel]);
      return result.join(', ');
    }
    return values[column.dropdownConfiguration.bindLabel];
  }

  public getPages(n: number): number[] {
    let pages = [];
    if (this.totalPages.length < n) {
      for (let i = this.totalPages.length - 1; i >= 0; i--) {
        pages.push(i);
      }
      return pages;
    }
    if (this.currentPage < n) {
      for (let i = n - 1; i >= 0; i--) {
        pages.push(i);
      }
    } else {
      const count = Math.floor(n / 2);
      const max = this.currentPage + count >= this.totalPages.length ? this.totalPages.length - 1 : this.currentPage + count;
      for (let i = max; i >= this.currentPage - count; i--) {
        pages.push(i);
      }
    }
    return pages;
  }

  public reload(keepSelectedItems: boolean = false): Observable<boolean> {
    return this.searchAsync(null, null, keepSelectedItems);
  }

  public search(advancedFilter?: boolean) {
    this.searchAsync(advancedFilter).subscribe();
  }

  public trackRecords(record: any) {
    return record ? record.id : undefined;
  }

  public searchAsync(advancedFilter?: boolean, currentPage?: number, keepSelectedItems: boolean = true): Observable<boolean> {
    this.loading = true;
    const request = this.buildRequest(currentPage, advancedFilter);
    if (this.option.localData) {
      this.localData = this.option.localData();
      this.searchLocalItems(request).subscribe((response) => {
        this.bindResultData(response, keepSelectedItems);
      });
    } else {
      if (!this.option.serviceProvider || !this.option.serviceProvider.searchAsync) throw new Error('!this.option.serviceProvider.searchAsync');
      this.option.serviceProvider.searchAsync(request).subscribe((response) => {
        this.bindResultData(response, keepSelectedItems);
      });
    }
    return of(true);
  }

  private checkedAllPageItems(): boolean {
    if (!this.selectedItems || !this.items || this.items.length == 0) {
      return false;
    }
    let check = true;
    this.items.forEach((item) => {
      const currentItem = this.selectedItems.find(s => s.id === item.id);
      if (!currentItem) {
        check = false;
        return check;
      }
    });
    return check;
  }

  private setDefaultOrder(request: any): void {
    if (this.option.defaultOrderBy) this.orderBy = this.option.defaultOrderBy;
    if (this.option.defautOrderDirection) this.direction = this.option.defautOrderDirection;
    request.orderBy = this.orderBy;
    request.direction = this.direction;
    if (!this.orderBy) {
      this.orderBy = "Id";
      this.direction = TableConstant.Direction.ASC;
    }
  }

  public setFilter(key: string, value: any) {
    this.request[key] = value;
    this.currentPage = 0;
    this.filter[key] = value;
    if (this.option && this.option.request) this.option.request.pageIndex = 0;
  }

  private retrieveAdvancedFilters(advancedFilter: boolean, request: any): void {
    this.filterColumns.forEach((column: TableColumn) => {
      let value = this.filter[column.valueRef()];
      if (value == undefined || value == 'undefined') {
        value = '';
      }
      if (advancedFilter == true) {
        if (column.type && (column.type.toLowerCase() == 'date' || column.type.toLowerCase() == 'datetime' || column.type.toLowerCase() == 'time')) {
          if (value && value != '') {
            const datetimeValues = <Date[]>value;
            if (datetimeValues.length == 1) {
              request[column.valueRef() + 'From'] = this.convertDatetime(datetimeValues[0], 'From');
            } else if (datetimeValues.length == 2) {
              request[column.valueRef() + 'From'] = this.convertDatetime(datetimeValues[0], 'From');
              request[column.valueRef() + 'To'] = this.convertDatetime(datetimeValues[1], 'To');
            }
          }
        } else {
          request[column.valueRef()] = value;
        }
      } else {
        request[column.valueRef()] = value;
      }
    });
  }

  private buildRequest(currentPage?: number, advancedFilter?: boolean): any {
    this.previousRequest = Object.assign({}, this.request);
    let searchText = this.filter.searchText;
    this.textSearched = this.filter.searchText;
    if (searchText == undefined || searchText == 'undefined') {
      searchText = '';
    }
    this.request.pageSize = this.option.request.pageSize;
    if (!this.option.paging) {
      this.request.pageSize = 999999;
      this.pageSize = 999999;
    }
    this.request.searchText = searchText;
    this.request.direction = this.direction;
    this.request.orderBy = this.orderBy;
    if (!this.orderBy) this.setDefaultOrder(this.request);
    this.retrieveAdvancedFilters(advancedFilter, this.request);
    this.request.pageIndex = this.option.request.pageIndex;
    if (currentPage) {
      this.request.pageIndex = currentPage;
      this.currentPage = currentPage;
    }

    const changes = this.dataService.compareObjects(this.previousRequest, this.request);
    const isChanged = changes.filter(x => ['pageIndex', 'pageSize'].indexOf(x.propertyName) < 0).length > 0;
    if (isChanged) {
      this.option.request.pageIndex = 0;
      this.request.pageIndex = 0;
      this.currentPage = 0;
    }
    return this.request;
  }

  private convertDatetime(dt: Date, type: string): string {
    if (!dt) return '';
    let convertedDatetime = new Date(dt);
    let days = convertedDatetime.getDate().toString();
    if (days.length < 2) days = '0' + days;
    let months = (convertedDatetime.getMonth() + 1).toString();
    if (months.length < 2) months = '0' + months;
    let hours = convertedDatetime.getHours().toString();
    if (hours.length < 2) hours = '0' + hours;
    let minutes = convertedDatetime.getMinutes().toString();
    if (minutes.length < 2) minutes = '0' + minutes;
    const year = convertedDatetime.getFullYear();

    switch (this.option.datetimeFormat.format) {
      case 'dd/MM/yyyy':
        return days + '/' + months + '/' + year + (type == 'From' ? ' 00:00' : ' 23:59');
      case 'dd/MM/yyyy HH:mm':
        return days + '/' + months + '/' + year + ' ' + hours + ':' + minutes;
      case 'MM/dd/yyyy':
        return months + '/' + days + '/' + year + (type == 'From' ? ' 00:00' : ' 23:59');
      case 'MM/dd/yyyy HH:mm':
        return months + '/' + days + '/' + year + ' ' + hours + ':' + minutes;
    }
  }

  private searchLocalItems(request: any): Observable<any> {
    let result: any[] = this.localData;
    const orderBy = this.dataService.getField(request.orderBy, true);
    if (request.direction == TableConstant.Direction.ASC) {
      if (!this.option.sort) {
        result = result.sort((a: any, b: any) => a[orderBy] > b[orderBy] ? 1 : a[orderBy] === b[orderBy] ? 0 : -1);
      } else {
        result = result.sort((a: any, b: any) => this.option.sort(a, b, orderBy));
      }
    } else {
      if (!this.option.sort) {
        result = result.sort((b: any, a: any) => a[orderBy] > b[orderBy] ? 1 : a[orderBy] === b[orderBy] ? 0 : -1);
      } else {
        result = result.sort((b: any, a: any) => this.option.sort(a, b, orderBy));
      }
    }

    let items: any[] = [];
    if (request.pageIndex >= 0 && request.pageSize > 0) {
      const localResult = [];
      if (request.searchText && this.option.searchFields && this.option.searchFields.length > 0) {
        this.option.searchFields.forEach(field => {
          const response = result.filter(s => this.fuzzysearch(request.searchText, s[field]));
          if (response) {
            response.forEach((item) => {
              if (localResult.findIndex(s => s.id == item.id) == -1) {
                localResult.push(item);
              }
            });
          }
        });
        result = localResult;
      }

      var filter = {};
      this.retrieveAdvancedFilters(true, filter);
      if (this.filterColumns) {
        this.filterColumns.forEach((column) => {
          var value = filter[column.valueRef()];
          if (value) {
            result = result.filter(s => this.fuzzysearch(value, s[column.valueRef()]));
          }
        });
      }
      items = result.filter(s => (result.indexOf(s) >= request.pageIndex * request.pageSize) && (result.indexOf(s) < (request.pageIndex + 1) * request.pageSize));
    }
    const response: TableResponse<any> = {
      items: items,
      totalRecords: result.length
    };
    return of(response).pipe(delay(250));
  }

  private convertUCode(c: string): string {
    if ('a'.indexOf(c) > -1) return 'a';
    if ('d'.indexOf(c) > -1) return 'd';
    if ('o'.indexOf(c) > -1) return 'o';
    if ('u'.indexOf(c) > -1) return 'u';
    if ('i'.indexOf(c) > -1) return 'i';
    if ('y'.indexOf(c) > -1) return 'y';
    if ('e'.indexOf(c) > -1) return 'e';
    return c;
  }

  private fuzzysearch(needle: any, haystack: any): boolean {
    if (!needle || !haystack) return false;
    const haystackLC = this.removeAllSpaces(haystack.toString().toLowerCase());
    const needleLC = this.removeAllSpaces(needle.toString().toLowerCase());
    const hlen = haystack.toString().length;
    const nlen = needleLC.toString().length;
    if (nlen > hlen) {
      return false;
    }
    if (nlen === hlen) {
      return needleLC === haystackLC;
    }
    outer: for (let i = 0, j = 0; i < nlen; i++) {
      const nch = this.convertUCode(needleLC[i]);
      while (j < hlen) {
        if (this.convertUCode(haystackLC[j++]) === nch) {
          continue outer;
        }
      }
      return false;
    }
    return true;
  }

  private removeAllSpaces(str?: string): string {
    if (!str) return '';
    return str.replace(/\s/g, '');
  }

  private bindResultData(response: TableResponse<any>, keepSelectedItems: boolean = true): void {
    this.items = response.items;
    this.totalRecords = response.totalRecords;
    this.calculatePages();

    if (!keepSelectedItems) {
      this.selectedItems = [];
    }

    this.selectedAll = this.checkedAllPageItems();

    if (this.currentPage > this.totalPages.length - 1) {
      this.recursiveCounter++;
      if (this.recursiveCounter < 3) {
        this.searchAsync(null, this.totalPages.length - 1).subscribe();
      }
    }
    this.loading = false;
  }

  private calculatePages(): void {
    let pages = Math.floor(this.totalRecords / this.pageSize);
    if (pages <= 0) {
      pages = 1;
    }
    if (this.totalRecords % this.pageSize > 0) {
      pages += 1;
    }
    if (this.totalRecords < this.pageSize) {
      pages = 1;
    }
    this.totalPages = [];
    for (let i = 0; i < pages; i++) {
      this.totalPages.push(i);
    }
  }

  private resetSortAsync(currentColumn: TableColumn): void {
    this.option.mainColumns = this.option.mainColumns.map(column => {
      if (column.id !== currentColumn.id) column.direction = undefined;
      return column;
    });
  }

  private initTableTableTexts(): void {
    if (!this.option.displayText) {
      this.option.displayText = new TableText();
    } else {
      if (!this.option.displayText) this.option.displayText.placeholderSearch = TableConstant.DisplayText.PlaceholderSearch;
      if (!this.option.displayText.btnSearch) this.option.displayText.btnSearch = TableConstant.DisplayText.BtnSearch;
      if (!this.option.displayText.btnReset) this.option.displayText.btnReset = TableConstant.DisplayText.BtnReset;
      if (!this.option.displayText.action) this.option.displayText.action = TableConstant.DisplayText.Action;
      if (!this.option.displayText.selectPageSize) this.option.displayText.selectPageSize = TableConstant.DisplayText.SelectPageSize;
      if (!this.option.displayText.deleteTitle) this.option.displayText.deleteTitle = TableConstant.DisplayText.DeleteTitle;
      if (!this.option.displayText.btnAcceptTitle) this.option.displayText.btnAcceptTitle = TableConstant.DisplayText.BtnAcceptTitle;
      if (!this.option.displayText.btnCancelTitle) this.option.displayText.btnCancelTitle = TableConstant.DisplayText.BtnCancelTitle
      if (!this.option.displayText.filterTitle) this.option.displayText.filterTitle = TableConstant.DisplayText.FilterTitle;
      if (!this.option.displayText.applyFilter) this.option.displayText.applyFilter = TableConstant.DisplayText.ApplyFilter;
      if (!this.option.displayText.detailTitle) this.option.displayText.detailTitle = TableConstant.DisplayText.DetailTitle;
      if (!this.option.displayText.pageTitle) this.option.displayText.pageTitle = TableConstant.DisplayText.PageTitle;
      if (!this.option.displayText.advancedSearchTitle) this.option.displayText.advancedSearchTitle = TableConstant.DisplayText.AdvancedSearchTitle;
      if (!this.option.displayText.advancedBtnTitle) this.option.displayText.advancedBtnTitle = TableConstant.DisplayText.AdvancedBtnTitle;
      if (!this.option.displayText.advancedBtnCancelTitle) this.option.displayText.advancedBtnCancelTitle = TableConstant.DisplayText.AdvancedBtnCancelTitle;
      if (!this.option.displayText.allTitle) this.option.displayText.allTitle = TableConstant.DisplayText.AllTitle;
    }
  }

  private initTableTableMessages(): void {
    if (!this.option.message) {
      this.option.message = new TableMessage();
    } else {
      if (!this.option.message.notFoundMessage) this.option.message.notFoundMessage = TableConstant.Message.NotFoundMessage;
      if (!this.option.message.foundMessage) this.option.message.foundMessage = TableConstant.Message.FoundMessage;
      if (!this.option.message.invalidFormatMessage) this.option.message.invalidFormatMessage = TableConstant.Message.InvalidFormatMessage;
      if (!this.option.message.selectedItemsMessage) this.option.message.selectedItemsMessage = TableConstant.Message.SelectedItemsMessage;
      if (!this.option.message.confirmSelectAllRecordsMessage) this.option.message.confirmSelectAllRecordsMessage = TableConstant.Message.ConfirmSelectAllRecordsMessage;
      if (!this.option.message.confirmClearAllRecordsMessage) this.option.message.confirmClearAllRecordsMessage = TableConstant.Message.ConfirmClearAllRecordsMessage;
      if (!this.option.message.deleteMessage) this.option.message.deleteMessage = TableConstant.Message.DeleteMessage;
      if (!this.option.message.loadingMessage) this.option.message.loadingMessage = TableConstant.Message.LoadingMessage;
      if (!this.option.message.refMessage) this.option.message.refMessage = TableConstant.Message.RefMessage;
    }
  }

  private initMainColumns(): void {
    if (!this.option.mainColumns) {
      this.option.mainColumns = [];
    }
    this.option.mainColumns.forEach((column) => {
      if (!column.textAlign) column.textAlign = TableConstant.TextAlign.Left;
      if (column && !column.id) {
        column.id = this.dataService.newGuid();
      }
      if (column.allowFilter) {
        if (column.type === TableColumnType.Dropdown) {
          if (!column.dropdownConfiguration) throw new Error('!column.dropdownConfiguration');
        }
        this.filterColumns.push(column);
        this.filter[column.valueRef()] = null;
      }
    });
    this.option.mainColumns = this.option.mainColumns.sort((a: TableColumn, b: TableColumn) => a.order > b.order ? 1 : a.order === b.order ? 0 : -1);
  }


  private registerEvents() {
    if (this.gotoRef) {
      this._render.listen(this.gotoRef.nativeElement, 'keydown', ($event) => {
        const value = $event.which;
        let max = '9999999';
        if (this.gotoRef.nativeElement.attributes['max']) {
          max = this.gotoRef.nativeElement.attributes['max'].value;
        }
        if ((value >= 48 && value <= 57) || (value >= 96 && value <= 105) || value == 8 || value == 13) {
          if (value >= 48 && value <= 57) {
            if (parseInt(max) < parseInt($event.target.value + (parseInt(value) - 48))) {
              $event.preventDefault();
              $event.target.value = max;
            }
          } else if (value >= 96 && value <= 105) {
            if (parseInt(max) < parseInt($event.target.value + (parseInt(value) - 96))) {
              $event.preventDefault();
              $event.target.value = max;
            }
          }
          else return;
        } else {
          $event.preventDefault();
        }
      });
    }
  }
}
