import { Component, OnInit, AfterViewInit } from "@angular/core";
import { DefaultLayoutService } from './layout.service';
import { ActivatedRoute, Router, NavigationEnd } from '@angular/router';
import { AggregatorService } from '../shared/services/aggregator.service';
import { KeyConst } from '../shared/constants/key.const';
import { NotificationDetail } from '../shared/models/notification.model';
import { MenuTab, Breadcrumb } from '../shared/models/base.model';
import { ActionService } from '../shared/services/action.service';
import { AuthenticationService } from '../auth/auth.service';
import { AuthenticationLoginResponse } from '../auth/auth.model';
import { CacheService } from '../shared/services/cache.service';

@Component({
    templateUrl: './backsite-layout.component.html',
    styleUrls:['./backsite-layout.component.scss']
})

export class AdminLayoutComponent implements OnInit, AfterViewInit {
    public notifications: NotificationDetail[] = [];
    public logo: string = 'https://cmcglobal.com.vn/css/image-common/Combined%20Shape.svg';
    public title: string = 'CMC Global - Aspire to inspire the Digital World';;
    public menuTabs: MenuTab[];
    public isModalShow: boolean = false;
    public modalImageSrc: string = '';
    public imageAltText: string = '';
    public breadcrumbs: Breadcrumb[] = [];
    public breadcrumb: Breadcrumb;
    public url: string = '/dashboard';
    public authUrl: string = '/auth';
    public setActive: boolean = false;
    public menuType: string = 'TOP';
    public isMobile: boolean;

    constructor(
        public workspaceLayoutService: DefaultLayoutService,
        public route: ActivatedRoute,
        public authenticationService: AuthenticationService,
        protected router: Router,
        protected actionService: ActionService,
        protected aggregatorService: AggregatorService,
        protected cacheService: CacheService
    ) {
        this.registerEvents();
    }

    ngOnInit(): void {
        this.isMobile = this.actionService.isMobile();
        if (this.isMobile) this.menuType = 'LEFT';
        this.aggregatorService.subscribe(KeyConst.ViewImage, (image: any) => {
            this.isModalShow = true;
            this.modalImageSrc = image.src;
            this.imageAltText = image.altText;
        });

        this.route.data.subscribe(data => {
            if (data) {
                if (data.menuTabs) this.menuTabs = <MenuTab[]>data.menuTabs;
                if (!this.menuTabs || this.menuTabs.length == 0) {
                    this.menuTabs = this.cacheService.get(KeyConst.Menu);
                }
                if (data.menuType) this.menuType = <string>data.menuType;
                if (data.logo) this.logo = <string>data.logo;
                if (data.title)<string>data.title;
                if (data.url) this.url = <string>data.url;
                if (data.authUrl) this.authUrl = <string>data.authUrl;
                if (data.breadcrumb) this.breadcrumb = <Breadcrumb>data.breadcrumb;
                if (!this.breadcrumb) {
                    this.breadcrumb = new Breadcrumb({
                        label: 'Home',
                        url: this.url
                    });
                } else {
                    this.breadcrumb.url = this.url;
                }
                if (this.router.url == '/') this.router.navigate([this.url]);
                if (!this.authenticationService.currentUser) this.router.navigate([this.authUrl]);
            }
        });
    }

    ngAfterViewInit(): void {
        this.router.events.subscribe((event) => {
            if (event instanceof NavigationEnd) {
                if (this.router.url == '/') {
                    this.router.navigate([this.url]);
                    this.setActive = !this.setActive;
                }
            }
        });
    }

    public closeImage(): void {
        this.isModalShow = false;
    }

    public changeMenu(items: Breadcrumb[]): void {
        this.breadcrumbs = items;
        this.breadcrumbs.splice(0, 0, this.breadcrumb);
        this.aggregatorService.publish(KeyConst.MenuChanged, this.breadcrumbs);
    }

    private registerEvents(): void {
        this.aggregatorService.subscribe(KeyConst.LoggedIn, (response: AuthenticationLoginResponse) => {
            if (!response) return;
            if (response.menuTabs) {
                this.menuTabs = response.menuTabs;
                this.cacheService.set(KeyConst.Menu, this.menuTabs);
            }
            this.authenticationService.currentUser = response.user;
            this.workspaceLayoutService.verticalEffect = this.menuType == 'LEFT' ? 'overlay' : 'shrink';
            this.actionService.executeAsync(() => {
                this.setActive = !this.setActive;
            });
        });
    }
}
