import { Type, EventEmitter } from '@angular/core';
import { ChangeType } from '../enums/base.enum';
import { Observable } from 'rxjs';

export class MockData<T> {
    mockData?: any;
    constructor(init?: Partial<MockData<T>>) {
        Object.assign(this, init);
    };
}

export class BaseRequest<T> extends MockData<T> {
    payload?: T;
    constructor(init?: Partial<BaseRequest<T>>) {
        super();
        Object.assign(this, init);
    };
}

export class BaseResponse<T> {
    data?: T;
    success?: boolean;
    code?: number;
    message?: string;
    constructor(init?: Partial<BaseResponse<T>>) {
        Object.assign(this, init);
    };
}

export class SearchBaseRequest extends MockData<any> {
    searchText?: string;
    pageIndex?: number;
    pageSize?: number;
    direction?: string;
    orderBy?: string;
    all?: boolean;
    constructor(init?: Partial<SearchBaseRequest>) {
        super();
        Object.assign(this, init);
    };
}

export class SearchBaseResponse<T> {
    items?: T[];
    success?: boolean;
    code?: number;
    message?: string;
    constructor(init?: Partial<SearchBaseResponse<T>>) {
        Object.assign(this, init);
    };
}

export class BaseTemplate {
    data?: any;
    validationKey?: string;
    template?: Type<any>;
    constructor(init?: Partial<BaseTemplate>) {
        Object.assign(this, init);
    };
}

export class AggregatorViewModel {
    value?: EventEmitter<any>;
    key?: string;
    currentEmitter: any;
    constructor(init?: Partial<AggregatorViewModel>) {
        Object.assign(this, init);
    };
}

export class ButtonDefinition {
    action: string;
    title: string;
    icon: string;
    hide: boolean;
    disabled: boolean;
    lazyload: boolean;
    style: 'default' | 'success' | 'warning' | 'danger' | 'link' | 'info' | 'inverse' | 'primary' | 'outline-primary' | 'outline-inverse' = 'default';
    constructor(init?: Partial<ButtonDefinition>) {
        Object.assign(this, init);
    }
}

export class ToolbarActionPayload {
    loadedCallback: Function;
    action: string;
    callback: Function;
    constructor(init?: Partial<ToolbarActionPayload>) {
        Object.assign(this, init);
    }
}

export class ObjectChange {
    propertyName: string;
    oldValue: any;
    newValue: any;
    changeType: ChangeType;
    constructor(init?: Partial<ObjectChange>) {
        Object.assign(this, init);
    }
}

