import { Component, OnDestroy, Input, Output, EventEmitter } from '@angular/core';
import { FileViewModel, FileResponse, FileRequest } from '../file/file.model';
import { ModalService } from '../modals/modal.service';
import { TemplateViewModel } from '../modals/modal.model';
import { GalleryComponent } from './gallery/gallery.component';
import { DataService } from '../services/data.service';
import { of, Observable } from 'rxjs';
import { AuthenticationService } from '../../auth/auth.service';

@Component({
  selector: 'katana-viewer',
  templateUrl: './viewer.component.html',
  styleUrls: ['./viewer.component.scss']
})

export class ViewerComponent implements OnDestroy {
  public isLoading: boolean = false;
  public images: FileViewModel[] = [];
  public useLibrary: boolean = true;
  @Input() public headers: string[] = ['#', 'Tn', 'Kch thc', 'Ngy to'];
  @Input() public multiple: boolean;
  @Input() public galleryItems: (request: FileRequest) => Observable<FileResponse>;
  @Input() public componentTitle: string = 'Thm nh';
  @Input() public uploadDialogTitle: string = 'Chnh sa nh';
  @Input() public uploadDialogIcon: string = 'fa fa-twitter';
  @Input() public uploadTitle: string = 'Ti ln';
  @Input() public uploadIcon: string = 'fa fa-exchange';
  @Input() public openGalleryTitle: string = 'Chn t th vin';
  @Input() public openGalleryIcon: string = 'fa fa-google-wallet';
  @Input() public title: string;
  @Input() public model: any[] = [];
  @Input() public zoomPrimaryImage: boolean = true;
  @Input() public editable: boolean = true;
  @Input() public cutRatio: number = 1 / 1;
  @Input() public resizeToWidth: number;
  @Input() public maintainAspectRatio: boolean;
  @Input() validationName: string;
  @Input() validationScope: string;
  @Input() emitNullOnDestroy: boolean = false;
  @Output() public modelChange: EventEmitter<any[]> = new EventEmitter();
  @Output() public onOpenUserMediaGallery: EventEmitter<void> = new EventEmitter();

  constructor(
    private _modalService: ModalService,
    private _dataService: DataService,
    protected authenticationService: AuthenticationService
  ) { }

  ngOnDestroy(): void {
    if (this.emitNullOnDestroy === true)
      this.modelChange.emit([]);
  }

  public addImage(file: FileViewModel): void {
    this.images = this.model;
    if (!this.images) this.images = [];
    this.images.push({
      src: file.src,
      name: file.name,
      size: file.size,
      createdDate: new Date(),
      lastModifiedDate: new Date(),
      createdBy: this.authenticationService.currentUser ? this.authenticationService.currentUser.id : null,
      lastModifiedBy: this.authenticationService.currentUser ? this.authenticationService.currentUser.id : null
    });
    this.modelChange.emit(this.images);
  }

  public setLoading(loading: boolean): void {
    this.isLoading = loading;
  }

  public openUserMediaGallery(): void {
    this.onOpenUserMediaGallery.emit();
    var items = <any[]>this._dataService.cloneItems(this.images);
    if (!items) items = [];
    this._modalService.showTemplateDialog(new TemplateViewModel({
      template: GalleryComponent,
      title: this.openGalleryTitle,
      icon: this.openGalleryIcon,
      customSize: 'modal-lg',
      data: {
        headers: this.headers,
        items: !this.useLibrary ? () => {
          return of(new FileResponse({
            code: '200',
            status: true,
            totalRecords: 0,
            items: []
          }));
        } : () => {
          if (!this.galleryItems) {
            return of(new FileResponse({
              code: '200',
              status: true,
              totalRecords: items.length,
              items: items
            }));
          } else {
            return this.galleryItems;
          }
        }
      },
      acceptCallback: (response: FileViewModel[]) => {
        response.forEach(item => {
          this.images.push(item);
        });
        this.modelChange.emit(this.images);
      }
    }));
  }
}
