import { Component, OnDestroy, ViewChild, Input, Output, EventEmitter } from '@angular/core';
import * as Editor from '@ckeditor/ckeditor5-build-classic';
import { FileService } from '../file';
import { EditorAdapter } from './editor.adaptor';
import { AuthenticationService } from '../../auth/auth.service';
import { ActionService } from '../services/action.service';

@Component({
  selector: 'katana-editor',
  templateUrl: './editor.component.html',
  styleUrls: ['./editor.component.scss']
})

export class EditorComponent implements OnDestroy {
  @ViewChild('nativeEditor', { static: true }) public nativeEditor: any;
  @Input() public model: string = '';
  @Input() public plainText: string;
  @Input() public title: string;
  @Input() public disabled: boolean = false;
  @Input() public validationName: string;
  @Input() public validationScope: string;
  @Input() emitNullOnDestroy: boolean = false;
  @Output() public modelChange: EventEmitter<string> = new EventEmitter();
  @Input() public focus: boolean;
  @Output() public plainTextChange: EventEmitter<string> = new EventEmitter();
  public editor = Editor;
  public config: any = {
    height: '600px'
  };

  constructor(
    public fileService: FileService,
    public authenticationService: AuthenticationService,
    private _actionService: ActionService
  ) { }

  ngOnDestroy() {
    if (this.emitNullOnDestroy === true)
      this.modelChange.emit(null);
  }

  public change(html: string) {
    const plainText = this.nativeEditor.elementRef.nativeElement.textContent;
    this.plainTextChange.emit(plainText);
    this.modelChange.emit(html);
  }

  public ready(event: any, fileService: FileService, authenticationService: AuthenticationService): void {
    event.plugins.get('FileRepository').createUploadAdapter = function (loader: any) {
      return new EditorAdapter(loader, fileService, authenticationService);
    };
    if (this.focus) {
      this._actionService.executeAsync(() => {
        event.ui.getEditableElement().focus();
      });
    }
  }
}