import { Component, OnDestroy, Input, Output, EventEmitter } from '@angular/core';
import { FileViewModel } from '../file/file.model';

@Component({
    selector: 'katana-image-viewer',
    templateUrl: './image-viewer.component.html',
    styleUrls: ['./image-viewer.component.scss']
})

export class ImageViewerComponent implements OnDestroy {
    @Input() public uploadIcon: string;
    @Input() public uploadTitle: string;
    @Input() public multiple: boolean = false;
    @Input() public model;
    @Input() public size: 'small' | 'medium' | 'large' | 'full' | 'tiny' = 'small';
    @Input() public defaultImageUrl = 'https://cmcglobal.com.vn/css/image-common/Combined%20Shape.svg';
    @Input() public title: string;
    @Input() public editable: boolean = false;
    @Input() public customClasses: string;
    @Output() public imageRemoved: EventEmitter<string> = new EventEmitter();
    @Input() public radious: boolean = false;
    @Input() validationName: string;
    @Input() public cutRatio: number = 1 / 1;
    @Input() public resizeToWidth: number;
    @Input() public maintainAspectRatio: boolean;
    @Input() validationScope: string;
    @Input() emitNullOnDestroy: boolean = false;
    @Output() public modelChange: EventEmitter<any> = new EventEmitter();
    @Output() public selectedChange: EventEmitter<boolean> = new EventEmitter();

    ngOnDestroy(): void {
        if (this.emitNullOnDestroy === true)
            this.modelChange.emit(null);
    }

    public fileUploaded(file: FileViewModel): void {
        let model = this.model;
        if (!model) model = {};
        model.src = file.src;
        this.modelChange.emit(model);
    }

    public imageSrc(): string {
        if (this.model && this.model.src) {
            return this.model.src;
        } else {
            return this.defaultImageUrl;
        }
    }

    public removeImage(): void {
        this.model.src = this.defaultImageUrl;
        this.modelChange.emit(null);
        this.imageRemoved.emit(this.model.src);
    }

    public selectImage(item: any): void {
        if (!item) return;
        if (!item.selected) item.selected = false;
        item.selected = !item.selected;
        this.selectedChange.emit(item.selected);
    }
}
